  /** @hidden */
  static WidgetFastener = ControllerViewTrait.define<WidgetController, WidgetView, WidgetTrait>({
    viewType: WidgetView,
    observeView: true,
    willSetView(newWidgetView: WidgetView | null, oldWidgetView: WidgetView | null): void {
      this.owner.willSetWidgetView(newWidgetView, oldWidgetView);
    },
    onSetView(newWidgetView: WidgetView | null, oldWidgetView: WidgetView | null): void {
      this.owner.onSetWidgetView(newWidgetView, oldWidgetView);
    },
    didSetView(newWidgetView: WidgetView | null, oldWidgetView: WidgetView | null): void {
      this.owner.didSetWidgetView(newWidgetView, oldWidgetView);
    },
    viewDidApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean, widgetView: WidgetView): void {
      this.owner.themeWidgetView(theme, mood, timing, widgetView);
    },
    widgetViewDidSetHeader(newHeaderView: HtmlView | null, oldHeaderView: HtmlView | null): void {
      this.owner.header.setView(newHeaderView);
    },
    widgetViewDidSetHeaderTitle(newTitleView: HtmlView | null, oldTitleView: HtmlView | null): void {
      this.owner.header.setView(newTitleView);
    },
    widgetViewDidSetHeaderSubtitle(newSubtitleView: HtmlView | null, oldSubtitleView: HtmlView | null): void {
      this.owner.header.setView(newSubtitleView);
    },
    widgetViewDidSetFooter(newFooterView: HtmlView | null, oldFooterView: HtmlView | null): void {
      this.owner.footer.setView(newFooterView);
    },
    createView(): WidgetView | null {
      return this.owner.createWidgetView();
    },
    traitType: WidgetTrait,
    observeTrait: true,
    willSetTrait(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null): void {
      this.owner.willSetWidgetTrait(newWidgetTrait, oldWidgetTrait);
    },
    onSetTrait(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null): void {
      this.owner.onSetWidgetTrait(newWidgetTrait, oldWidgetTrait);
    },
    didSetTrait(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null): void {
      this.owner.didSetWidgetTrait(newWidgetTrait, oldWidgetTrait);
    },
    traitDidMount(widgetTrait: WidgetTrait): void {
      widgetTrait.addTraitConsumer(this.owner);
    },
    traitWillUnmount(widgetTrait: WidgetTrait): void {
      widgetTrait.removeTraitConsumer(this.owner);
    },
    widgetTraitDidSetTitle(newTitle: WidgetTitle | null, oldTitle: WidgetTitle | null, widgetTrait: WidgetTrait): void {
      this.owner.onSetWidgetTraitTitle(newTitle, oldTitle, widgetTrait);
    },
    widgetTraitDidSetSubtitle(newSubtitle: WidgetSubtitle | null, oldSubtitle: WidgetSubtitle | null, widgetTrait: WidgetTrait): void {
      this.owner.onSetWidgetTraitSubtitle(newSubtitle, oldSubtitle, widgetTrait);
    },
    widgetTraitWillSetGadget(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null, targetTrait: Trait): void {
      if (oldGadgetTrait !== null) {
        this.owner.removeGadgetTrait(oldGadgetTrait);
      }
    },
    widgetTraitDidSetGadget(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null, targetTrait: Trait): void {
      if (newGadgetTrait !== null) {
        this.owner.insertGadgetTrait(newGadgetTrait, targetTrait);
      }
    },
    detectGadgetModel(model: Model): Trait | null {
      return this.owner.detectGadgetModel(model);
    },
  });

  @ControllerViewTrait<WidgetController, WidgetView, WidgetTrait>({
    extends: WidgetController.WidgetFastener,
  })
  readonly widget!: ControllerViewTrait<this, WidgetView, WidgetTrait>;

