  readonly headerSubtitle!: ControllerView<this, HtmlView>;

  @ControllerView<WidgetController, HtmlView>({
    type: HtmlView,
    willSetView(newFooterView: HtmlView | null, oldFooterView: HtmlView | null): void {
      this.owner.willSetFooterView(newFooterView, oldFooterView);
    },
    onSetView(newFooterView: HtmlView | null, oldFooterView: HtmlView | null): void {
      this.owner.onSetFooterView(newFooterView, oldFooterView);
    },
    didSetView(newFooterView: HtmlView | null, oldFooterView: HtmlView | null): void {
      this.owner.didSetFooterView(newFooterView, oldFooterView);
    },
  })
  readonly footer!: ControllerView<this, HtmlView>;

  insertGadget(gadgetController: GadgetController, targetController: Controller | null = null): void {
    const gadgetFasteners = this.gadgetFasteners as ControllerFastener<this, GadgetController>[];
    let targetIndex = gadgetFasteners.length;
    if (targetController !== null) {
      for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
        const gadgetFastener = gadgetFasteners[i]!;
        if (gadgetFastener.controller === gadgetController) {
          return;
        } else if (gadgetFastener.controller === targetController) {
          targetIndex = i;
        }
      }
    }
    const gadgetFastener = this.createGadgetFastener(gadgetController);
    gadgetFasteners.splice(targetIndex, 0, gadgetFastener);
    gadgetFastener.setController(gadgetController, targetController);
    if (this.isMounted()) {
      gadgetFastener.mount();
    }
  }

  removeGadget(gadgetController: GadgetController): void {
    const gadgetFasteners = this.gadgetFasteners as ControllerFastener<this, GadgetController>[];
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      if (gadgetFastener.controller === gadgetController) {
        gadgetFastener.setController(null);
        if (this.isMounted()) {
          gadgetFastener.unmount();
        }
        gadgetFasteners.splice(i, 1);
        break;
      }
    }
  }

  protected createGadget(gadgetTrait: Trait): GadgetController | null {
    return null;
  }

  protected initGadget(gadgetController: GadgetController, gadgetFastener: ControllerFastener<this, GadgetController>): void {
    const gadgetTrait = gadgetController.gadget.trait;
    if (gadgetTrait !== null) {
      this.initGadgetTrait(gadgetTrait, gadgetFastener);
    }
    const gadgetView = gadgetController.gadget.view;
    if (gadgetView !== null) {
      this.initGadgetView(gadgetView, gadgetFastener);
    }
  }

  protected attachGadget(gadgetController: GadgetController, gadgetFastener: ControllerFastener<this, GadgetController>): void {
    const gadgetTrait = gadgetController.gadget.trait;
    if (gadgetTrait !== null) {
      this.attachGadgetTrait(gadgetTrait, gadgetFastener);
    }
    const gadgetView = gadgetController.gadget.view;
    if (gadgetView !== null) {
      this.attachGadgetView(gadgetView, gadgetFastener);
    }
  }

  protected detachGadget(gadgetController: GadgetController, gadgetFastener: ControllerFastener<this, GadgetController>): void {
    const gadgetView = gadgetController.gadget.view;
    if (gadgetView !== null) {
      this.detachGadgetView(gadgetView, gadgetFastener);
    }
    const gadgetTrait = gadgetController.gadget.trait;
    if (gadgetTrait !== null) {
      this.detachGadgetTrait(gadgetTrait, gadgetFastener);
    }
  }

  protected willSetGadget(newGadgetController: GadgetController | null, oldGadgetController: GadgetController | null,
                          gadgetFastener: ControllerFastener<this, GadgetController>): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerWillSetGadget !== void 0) {
        controllerObserver.controllerWillSetGadget(newGadgetController, oldGadgetController, gadgetFastener);
      }
    }
  }

  protected onSetGadget(newGadgetController: GadgetController | null, oldGadgetController: GadgetController | null,
                        gadgetFastener: ControllerFastener<this, GadgetController>): void {
    if (oldGadgetController !== null) {
      this.detachGadget(oldGadgetController, gadgetFastener);
    }
    if (newGadgetController !== null) {
      this.attachGadget(newGadgetController, gadgetFastener);
      this.initGadget(newGadgetController, gadgetFastener);
    }
  }

  protected didSetGadget(newGadgetController: GadgetController | null, oldGadgetController: GadgetController | null,
                         gadgetFastener: ControllerFastener<this, GadgetController>): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerDidSetGadget !== void 0) {
        controllerObserver.controllerDidSetGadget(newGadgetController, oldGadgetController, gadgetFastener);
      }
    }
  }

  insertGadgetTrait(gadgetTrait: Trait, targetTrait: Trait | null = null): void {
    const gadgetFasteners = this.gadgetFasteners as ControllerFastener<this, GadgetController>[];
    let targetController: GadgetController | null = null;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetController = gadgetFasteners[i]!.controller;
      if (gadgetController !== null) {
        if (gadgetController.gadget.trait === gadgetTrait) {
          return;
        } else if (gadgetController.gadget.trait === targetTrait) {
          targetController = gadgetController;
        }
      }
    }
    const gadgetController = this.createGadget(gadgetTrait);
    if (gadgetController !== null) {
      gadgetController.gadget.setTrait(gadgetTrait);
      this.insertChildController(gadgetController, targetController);
      if (gadgetController.gadget.view === null) {
        const gadgetView = this.createGadgetView(gadgetController);
        let targetView: HtmlView | null = null;
        if (targetController !== null) {
          targetView = targetController.gadget.view;
        }
        const widgetView = this.widget.view;
        if (widgetView !== null) {
          if (targetView === null) {
            targetView = widgetView.footer.view;
          }
          gadgetController.gadget.injectView(widgetView, gadgetView, targetView);
        } else {
          gadgetController.gadget.setView(gadgetView, targetView);
        }
      }
    }
  }

  removeGadgetTrait(gadgetTrait: Trait): void {
    const gadgetFasteners = this.gadgetFasteners as ControllerFastener<this, GadgetController>[];
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      const gadgetController = gadgetFastener.controller;
      if (gadgetController !== null && gadgetController.gadget.trait === gadgetTrait) {
        gadgetFastener.setController(null);
        if (this.isMounted()) {
          gadgetFastener.unmount();
        }
        gadgetFasteners.splice(i, 1);
        gadgetController.remove();
        return;
      }
    }
  }

