  /** @hidden */
  static GadgetFastener = ControllerFastener.define<WidgetController, GadgetController>({
    child: false,
    observe: true,
    willSetController(newGadgetController: GadgetController | null, oldGadgetController: GadgetController | null): void {
      this.owner.willSetGadget(newGadgetController, oldGadgetController, this);
    },
    onSetController(newGadgetController: GadgetController | null, oldGadgetController: GadgetController | null): void {
      this.owner.onSetGadget(newGadgetController, oldGadgetController, this);
    },
    didSetController(newGadgetController: GadgetController | null, oldGadgetController: GadgetController | null): void {
      this.owner.didSetGadget(newGadgetController, oldGadgetController, this);
    },
    controllerWillSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.willSetGadgetTrait(newGadgetTrait, oldGadgetTrait, this);
    },
    controllerDidSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.onSetGadgetTrait(newGadgetTrait, oldGadgetTrait, this);
      this.owner.didSetGadgetTrait(newGadgetTrait, oldGadgetTrait, this);
    },
    controllerWillSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.willSetGadgetView(newGadgetView, oldGadgetView, this);
    },
    controllerDidSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.onSetGadgetView(newGadgetView, oldGadgetView, this);
      this.owner.didSetGadgetView(newGadgetView, oldGadgetView, this);
    },
  });

  protected createGadgetFastener(gadgetController: GadgetController): ControllerFastener<this, GadgetController> {
    return new WidgetController.GadgetFastener(this, gadgetController.key, "gadget");
  }

  /** @hidden */
  readonly gadgetFasteners!: ReadonlyArray<ControllerFastener<this, GadgetController>>;

  protected getGadgetFastener(gadgetTrait: Trait): ControllerFastener<this, GadgetController> | null {
    const gadgetFasteners = this.gadgetFasteners;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      const gadgetController = gadgetFastener.controller;
      if (gadgetController !== null && gadgetController.gadget.trait === gadgetTrait) {
        return gadgetFastener;
      }
    }
    return null;
  }

  /** @hidden */
  protected mountGadgetFasteners(): void {
    const gadgetFasteners = this.gadgetFasteners;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      gadgetFastener.mount();
    }
  }

  /** @hidden */
  protected unmountGadgetFasteners(): void {
    const gadgetFasteners = this.gadgetFasteners;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      gadgetFastener.unmount();
    }
  }

  protected detectGadgetModel(model: Model): Trait | null {
    return null;
  }

  protected detectGadgetModels(widgetTrait: WidgetTrait): void {
    const model = widgetTrait.model;
    if (model !== null) {
      const childModels = model.childModels;
      for (let i = 0, n = childModels.length; i < n; i += 1) {
        const childModel = childModels[i]!;
        const gadgetTrait = this.detectGadgetModel(childModel);
        if (gadgetTrait !== null) {
          widgetTrait.insertGadget(gadgetTrait);
        }
      }
    }
  }

  protected detectGadgetController(controller: Controller): GadgetController | null {
    return null;
  }

  protected onInsertGadgetController(gadgetController: GadgetController, targetController: Controller | null): void {
    this.insertGadget(gadgetController, targetController);
  }

  protected onRemoveGadgetController(gadgetController: GadgetController): void {
    this.removeGadget(gadgetController);
  }

  protected override onInsertChildController(childController: Controller, targetController: Controller | null): void {
    super.onInsertChildController(childController, targetController);
    const gadgetController = this.detectGadgetController(childController);
    if (gadgetController !== null) {
      this.onInsertGadgetController(gadgetController, targetController);
    }
  }

  protected override onRemoveChildController(childController: Controller): void {
    super.onRemoveChildController(childController);
    const gadgetController = this.detectGadgetController(childController);
    if (gadgetController !== null) {
      this.onRemoveGadgetController(gadgetController);
    }
  }

  /** @hidden */
  protected override mountControllerFasteners(): void {
    super.mountControllerFasteners();
    this.mountGadgetFasteners();
  }

  /** @hidden */
  protected override unmountControllerFasteners(): void {
    this.unmountGadgetFasteners();
    super.unmountControllerFasteners();
  }

  protected override onMount(): void {
    super.onMount();
    const widgetTrait = this.widget.trait;
    if (widgetTrait !== null) {
      widgetTrait.addTraitConsumer(this);
    }
  }

  protected override onUnmount(): void {
    super.onUnmount();
    const widgetTrait = this.widget.trait;
    if (widgetTrait !== null) {
      widgetTrait.removeTraitConsumer(this);
    }
  }
