import {HtmlView} from "@swim/dom";
import {ControllerModel, ControllerView, ControllerFastener} from "@swim/controller";
import {DrawerView} from "@swim/window";
import type {GeoView} from "@swim/map"
import {DomainGroup} from "@swim/domain";
import {SurfaceView, MirrorController} from "@swim/prism";
import {AtlasEntityDistrict} from "./AtlasEntityDistrict";
import {AtlasMap} from "./AtlasMap";
import {AtlasLayers} from "./AtlasLayers";

export class AtlasController<M extends AtlasMap = AtlasMap> extends MirrorController {
  override isFullBleed(): boolean {
    return true;
  }

  protected initRoot(entityDistrict: AtlasEntityDistrict): void {
    this.layers.injectController();
  }

  protected initLayers(layersController: AtlasLayers): void {
    const toolbarView = this.toolbar.view;
    if (toolbarView !== null) {
      layersController.toolbar.setView(toolbarView);
    }
    const drawerView = this.drawer.view;
    if (drawerView !== null) {
      layersController.drawer.setView(drawerView);
    }
  }

  protected initLayer(layerView: GeoView): void {
    // hook
  }

  protected initMap(mapController: M): void {
    // hook
  }

  protected attachSurface(surfaceView: SurfaceView): void {
    const mapController = this.map.injectController();
    if (mapController !== null) {
      mapController.surface.setView(surfaceView);
      const mapView = mapController.base.view;
      if (mapView !== null) {
        const domainGroup = this.domains.model;
        if (domainGroup !== null) {
          const entityDistrict = this.root.injectController();
          if (entityDistrict !== null) {
            const layerView = entityDistrict.layer.injectView(mapView);
            if (layerView !== null) {
              this.initLayer(layerView);
            }
            entityDistrict.entities.setModel(domainGroup);
          }
        }
      }
    }
  }

  protected detachSurface(surfaceView: SurfaceView): void {
    const mapController = this.map.injectController();
    if (mapController !== null) {
      mapController.surface.setView(null);
    }
    this.historyService.replaceHistory({
      permanent: {
        lng: void 0,
        lat: void 0,
        zoom: void 0,
      },
    });
  }

  protected attachToolbar(toolbarView: HtmlView): void {
    const layersController = this.layers.controller;
    if (layersController !== null) {
      layersController.toolbar.setView(toolbarView);
    }
  }

  protected detachToolbar(toolbarView: HtmlView): void {
    const layersController = this.layers.controller;
    if (layersController !== null) {
      layersController.toolbar.setView(null);
    }
  }

  protected attachDrawer(drawerView: DrawerView): void {
    const layersController = this.layers.controller;
    if (layersController !== null) {
      layersController.drawer.setView(drawerView);
    }
  }

  protected detachDrawer(drawerView: DrawerView): void {
    const layersController = this.layers.controller;
    if (layersController !== null) {
      layersController.drawer.setView(null);
    }
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const mapController = this.map.controller;
    if (mapController !== null) {
      const mapView = mapController.base.view;
      if (mapView !== null) {
        const entityDistrict = this.root.injectController();
        if (entityDistrict !== null) {
          const layerView = entityDistrict.layer.injectView(mapView);
          if (layerView !== null) {
            this.initLayer(layerView);
          }
          entityDistrict.entities.setModel(domainGroup);
        }
      }
    }
  }

  @ControllerFastener<AtlasController, AtlasEntityDistrict>({
    type: AtlasEntityDistrict,
    onSetController(entityDistrict: AtlasEntityDistrict | null): void {
      if (entityDistrict !== null) {
        this.owner.initRoot(entityDistrict);
      }
    },
    createController(): AtlasEntityDistrict | null {
      return new AtlasEntityDistrict();
    },
  })
  readonly root!: ControllerFastener<this, AtlasEntityDistrict>;

  @ControllerFastener<AtlasController, AtlasLayers>({
    type: AtlasLayers,
    onSetController(layersController: AtlasLayers | null): void {
      if (layersController !== null) {
        this.owner.initLayers(layersController);
      }
    },
    createController(): AtlasLayers | null {
      return new AtlasLayers();
    },
  })
  readonly layers!: ControllerFastener<this, AtlasLayers>;

  @ControllerFastener<AtlasController, AtlasMap>({
    type: AtlasMap,
    onSetController(mapController: AtlasMap | null): void {
      if (mapController !== null) {
        this.owner.initMap(mapController);
      }
    },
  })
  readonly map!: ControllerFastener<this, M>;

  @ControllerView<AtlasController, SurfaceView>({
    type: SurfaceView,
    onSetView(newSurfaceView: SurfaceView | null, oldSurfaceView: SurfaceView | null): void {
      if (oldSurfaceView !== null) {
        this.owner.detachSurface(oldSurfaceView);
      }
      if (newSurfaceView !== null) {
        this.owner.attachSurface(newSurfaceView);
      }
    },
  })
  override readonly surface!: ControllerView<this, SurfaceView>;

  @ControllerView<AtlasController, HtmlView>({
    type: HtmlView,
    onSetView(newToolbarView: HtmlView | null, oldToolbarView: HtmlView | null): void {
      if (oldToolbarView !== null) {
        this.owner.detachToolbar(oldToolbarView);
      }
      if (newToolbarView !== null) {
        this.owner.attachToolbar(newToolbarView);
      }
    },
  })
  override readonly toolbar!: ControllerView<this, HtmlView>;

  @ControllerView<AtlasController, DrawerView>({
    type: DrawerView,
    onSetView(newDrawerView: DrawerView | null, oldDrawerView: DrawerView | null): void {
      if (oldDrawerView !== null) {
        this.owner.detachDrawer(oldDrawerView);
      }
      if (newDrawerView !== null) {
        this.owner.attachDrawer(newDrawerView);
      }
    },
  })
  override readonly drawer!: ControllerView<this, DrawerView>;

  @ControllerModel<AtlasController, DomainGroup>({
    type: DomainGroup,
    onSetModel(domainGroup: DomainGroup | null): void {
      if (domainGroup !== null) {
        this.owner.initDomains(domainGroup);
      }
    },
  })
  override readonly domains!: ControllerModel<this, DomainGroup>;
}
