  protected onUnselect(model: Model, selectionManager: SelectionManager): void {
    if (selectionManager.selections.length === 1) {
      const inventoryButton = this.inventoryButton.view;
      if (inventoryButton !== null) {
        inventoryButton.visibility.setState("hidden", View.Intrinsic);
      }
    }
  }

  protected showActivity(activityWindow: ActivityWindow): void {
    activityWindow.modalService.presentModal(activityWindow, {modal: true});
  }

  protected willShowActivity(activityWindow: ActivityWindow): void {
    activityWindow.display("block");
  }

  protected didShowActivity(activityWindow: ActivityWindow): void {
    const surfaceView = this.surface.view;
    if (surfaceView !== null) {
      surfaceView.setCulled(true);
      surfaceView.visibility.setState("hidden", View.Intrinsic);
    }
  }

  protected willHideActivity(activityWindow: ActivityWindow): void {
    const surfaceView = this.surface.view;
    if (surfaceView !== null) {
      surfaceView.visibility.setState(void 0, View.Intrinsic);
      surfaceView.setCulled(false);
    }
  }

  protected didHideActivity(activityWindow: ActivityWindow): void {
    activityWindow.display("none");

    const activityController = this.activity.controller;
    if (activityController !== null) {
      activityController.window.setView(null);
      activityController.remove();
    }

    //const sessionModel = this.session.model;
    //if (sessionModel !== null) {
    //  sessionModel.unselectAll();
    //}
  }

  addDomain(query: string): DomainTrait | null {
    let domainTrait: DomainTrait | null = null;
    const collectorController = this.collector.controller;
    const domainGroup = this.domains.model;
    if (collectorController !== null && domainGroup !== null) {
      domainTrait = collectorController.createDomain(query);
      if (domainTrait !== null && domainTrait.model !== null) {
        domainGroup.appendChildModel(domainTrait.model);
      }
    }
    return domainTrait;
  }

