  protected initSessionModel(sessionModel: SessionModel): void {
    this.domains.setModel(sessionModel.domains.model);
    const statusTrait = sessionModel.getTrait(StatusTrait);
    if (statusTrait !== null) {
      this.status.setTrait(statusTrait);
    }

    const magnifierController = this.magnifier.controller;
    if (magnifierController !== null) {
      magnifierController.session.setModel(sessionModel);
    }
  }

  protected attachSessionModel(sessionModel: SessionModel): void {
    const selectionManager = sessionModel.selectionService.manager;
    if (selectionManager !== null) {
      selectionManager.addModelManagerObserver(this.session);
    }
  }

  protected detachSessionModel(sessionModel: SessionModel): void {
    const selectionManager = sessionModel.selectionService.manager;
    if (selectionManager !== null) {
      selectionManager.removeModelManagerObserver(this.session);
    }
 }

  protected willSetSessionModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
    // hook
  }

  protected onSetSessionModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
    if (oldSessionModel !== null) {
      this.detachSessionModel(oldSessionModel);
    }
    if (newSessionModel !== null) {
      newSessionModel.mount();
      this.attachSessionModel(newSessionModel);
      this.initSessionModel(newSessionModel);
    }
  }

  protected didSetSessionModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
    // hook
  }

  @ControllerModel<ShellController, SessionModel, never, SelectionManagerObserver>({
    type: SessionModel,
    willSetModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
      this.owner.willSetSessionModel(newSessionModel, oldSessionModel);
    },
    onSetModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
      this.owner.onSetSessionModel(newSessionModel, oldSessionModel);
    },
    didSetModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
      this.owner.didSetSessionModel(newSessionModel, oldSessionModel);
    },
    selectionManagerDidSelect(model: Model, index: number, options: SelectionOptions | null, selectionManager: SelectionManager): void {
      this.owner.onSelect(model, index, options, selectionManager);
    },
    selectionManagerWillUnselect(model: Model, selectionManager: SelectionManager): void {
      this.owner.onUnselect(model, selectionManager);
    },
  })
  readonly session!: ControllerModel<this, SessionModel> & SelectionManagerObserver;

