import {HtmlView} from "@swim/dom";
import {
  ControllerService,
  HistoryState,
  HistoryManager,
  HistoryService,
  ControllerModel,
  ControllerView,
  ControllerFastener,
  CompositeController,
} from "@swim/controller";
import {TableView} from "@swim/table";
import {DrawerView} from "@swim/window";
import {DomainGroup} from "@swim/domain";
import {PrismPlugin, PrismManager, PrismService, MirrorPlugin, SurfaceView} from "@swim/prism";
import {ReflectionController} from "./ReflectionController";

export class ReflectorController extends CompositeController {
  constructor() {
    super();
    Object.defineProperty(this, "activeReflection", {
      value: null,
      enumerable: true,
      configurable: true,
    });
  }

  protected initSurface(surfaceView: SurfaceView): void {
    const activeReflection = this.activeReflection;
    if (activeReflection !== null) {
      activeReflection.surface.setView(surfaceView);
    }
  }

  protected initToolbar(toolbarView: HtmlView): void {
    const activeReflection = this.activeReflection;
    if (activeReflection !== null) {
      activeReflection.toolbar.setView(toolbarView);
    }
  }

  protected initDrawer(drawerView: DrawerView): void {
    const activeReflection = this.activeReflection;
    if (activeReflection !== null) {
      activeReflection.drawer.setView(drawerView);
    }
  }

  protected initMirrorList(listView: TableView): void {
    const plugins = this.prismService.plugins;
    for (let i = 0, n = plugins.length; i < n; i += 1) {
      const plugin = plugins[i];
      if (plugin instanceof MirrorPlugin) {
        this.insertPlugin(plugin);
      }
    }
  }

  protected insertPlugin(plugin: MirrorPlugin): void {
    let controllerFastener = this.getControllerFastener(plugin.id) as ControllerFastener<this, ReflectionController> | null;
    if (controllerFastener === null) {
      controllerFastener = new ReflectorController.ReflectionFastener(this, plugin.id, "reflection");
      this.setControllerFastener(plugin.id, controllerFastener);
      const reflectionController = new ReflectionController(plugin);
      controllerFastener.setController(reflectionController);

      const fragment = this.historyService.historyState.fragment;
      if (this.activeReflection === null && (fragment === void 0 || fragment === plugin.id)) {
        this.setActiveReflection(reflectionController);
        if (fragment === void 0) {
          this.historyService.replaceHistory({fragment: reflectionController.plugin.id});
        }
      }
    }
  }

  protected removePlugin(plugin: MirrorPlugin): void {
    const controllerFastener = this.getControllerFastener(plugin.id) as ControllerFastener<this, ReflectionController> | null;
    if (controllerFastener !== null) {
      controllerFastener.removeController();
      this.setControllerFastener(plugin.id, null);
    }
  }

  protected onInsertReflection(reflectionController: ReflectionController): void {
    const listView = this.mirrorList.view;
    if (listView !== null) {
      reflectionController.row.injectView(listView, void 0, void 0, reflectionController.plugin.id);
    }

    const domainGroup = this.domains.model;
    if (domainGroup !== null) {
      reflectionController.domains.setModel(domainGroup);
    }
  }

  protected onRemoveReflection(reflectionController: ReflectionController): void {
    if (reflectionController === this.activeReflection) {
      this.setActiveReflection(null);
    }
    const rowView = reflectionController.row.view;
    if (rowView !== null) {
      rowView.remove();
    }
  }

  readonly activeReflection!: ReflectionController | null;

  setActiveReflection(newReflectionController: ReflectionController | null): void {
    const oldReflectionController = this.activeReflection;
    if (newReflectionController !== oldReflectionController) {
      if (oldReflectionController !== null) {
        this.deactivateReflection(oldReflectionController);
      }
      Object.defineProperty(this, "activeReflection", {
        value: newReflectionController,
        enumerable: true,
        configurable: true,
      });
      if (newReflectionController !== null) {
        this.activateReflection(newReflectionController);
      }
    }
  }

  protected activateReflection(reflectionController: ReflectionController): void {
    const rowView = reflectionController.row.view;
    if (rowView !== null) {
      const leafView = rowView.leaf.view;
      if (leafView !== null) {
        leafView.highlight.focus(false);
      }
    }
    const surfaceView = this.surface.view;
    if (surfaceView !== null) {
      reflectionController.surface.setView(surfaceView);
    }
    const toolbarView = this.toolbar.view;
    if (toolbarView !== null) {
      reflectionController.toolbar.setView(toolbarView);
    }
    const drawerView = this.drawer.view;
    if (drawerView !== null) {
      reflectionController.drawer.setView(drawerView);
    }
  }

  protected deactivateReflection(reflectionController: ReflectionController): void {
    reflectionController.drawer.setView(null);
    reflectionController.toolbar.setView(null);
    reflectionController.surface.setView(null);
    const rowView = reflectionController.row.view;
    if (rowView !== null) {
      const leafView = rowView.leaf.view;
      if (leafView !== null) {
        leafView.highlight.unfocus(true);
      }
    }
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i];
      if (childController instanceof ReflectionController) {
        childController.domains.setModel(domainGroup);
      }
    }
  }

  @ControllerView<ReflectorController, SurfaceView>({
    type: SurfaceView,
    onSetView(surfaceView: SurfaceView | null): void {
      if (surfaceView !== null) {
        this.owner.initSurface(surfaceView);
      }
    },
  })
  readonly surface!: ControllerView<this, SurfaceView>;

  @ControllerView<ReflectorController, HtmlView>({
    type: HtmlView,
    onSetView(toolbarView: HtmlView | null): void {
      if (toolbarView !== null) {
        this.owner.initToolbar(toolbarView);
      }
    },
  })
  readonly toolbar!: ControllerView<this, HtmlView>;

  @ControllerView<ReflectorController, DrawerView>({
    type: DrawerView,
    onSetView(drawerView: DrawerView | null): void {
      if (drawerView !== null) {
        this.owner.initDrawer(drawerView);
      }
    },
  })
  readonly drawer!: ControllerView<this, DrawerView>;

  @ControllerView<ReflectorController, TableView>({
    type: TableView,
    onSetView(listView: TableView | null): void {
      if (listView !== null) {
        this.owner.initMirrorList(listView);
      }
    },
  })
  readonly mirrorList!: ControllerView<this, TableView>;

  static ReflectionFastener = ControllerFastener.define<ReflectorController, ReflectionController>({
    type: ReflectionController,
    observe: true,
    onSetController(newReflectionController: ReflectionController | null,
                    oldReflectionController: ReflectionController | null): void {
      if (oldReflectionController !== null) {
        this.owner.onRemoveReflection(oldReflectionController);
      }
      if (newReflectionController !== null) {
        this.owner.onInsertReflection(newReflectionController);
      }
    },
    controllerDidActivateReflection(reflectionController: ReflectionController): void {
      this.owner.historyService.pushHistory({fragment: reflectionController.plugin.id});
      this.owner.setActiveReflection(reflectionController);
    },
  });

  @ControllerModel<ReflectorController, DomainGroup>({
    type: DomainGroup,
    onSetModel(domainGroup: DomainGroup | null): void {
      if (domainGroup !== null) {
        this.owner.initDomains(domainGroup);
      }
    },
  })
  readonly domains!: ControllerModel<this, DomainGroup>;

  @ControllerService<ReflectorController, PrismManager>({
    type: PrismManager,
    extends: PrismService,
    prismManagerDidInsertPlugin(plugin: PrismPlugin, index: number): void {
      if (plugin instanceof MirrorPlugin) {
        this.owner.insertPlugin(plugin);
      }
    },
    prismManagerWillRemovePlugin(plugin: PrismPlugin): void {
      if (plugin instanceof MirrorPlugin) {
        this.owner.removePlugin(plugin);
      }
    },
  })
  readonly prismService!: PrismService<this>;

  @ControllerService<ReflectorController, HistoryManager>({
    type: HistoryManager,
    historyManagerDidPopHistory(historyState: HistoryState): void {
      this.owner.updateHistoryState(historyState);
    },
  })
  override readonly historyService!: HistoryService<this>;

  protected override onMount(): void {
    super.onMount();
    this.updateHistoryState(this.historyService.historyState);
  }

  protected updateHistoryState(historyState: HistoryState): void {
    const fragment = historyState.fragment;
    if (fragment !== void 0) {
      const reflectionController = this.getChildController(fragment) as ReflectionController | null;
      if (reflectionController !== null) {
        this.setActiveReflection(reflectionController);
      }
    }
  }
}
