import type {HtmlView} from "@swim/dom";
import {ControllerModel, ControllerView, CompositeController} from "@swim/controller";
import type {DrawerView} from "@swim/window";
import type {DomainGroup} from "@swim/domain";
import type {SurfaceView} from "../view/SurfaceView";

export abstract class MirrorController extends CompositeController {
  abstract isFullBleed(): boolean;

  abstract readonly surface: ControllerView<this, SurfaceView>;

  abstract readonly toolbar: ControllerView<this, HtmlView>;

  abstract readonly drawer: ControllerView<this, DrawerView>;

  abstract readonly domains: ControllerModel<this, DomainGroup>;
}
