import {View, PositionGestureInput} from "@swim/view";
import {HtmlView} from "@swim/dom";
import {
  ControllerModel,
  ControllerView,
  ControllerFastener,
  CompositeController,
} from "@swim/controller";
import {TextCellView, IconCellView, LeafView, RowView} from "@swim/table";
import {DrawerView} from "@swim/window";
import {DomainGroup} from "@swim/domain";
import {SurfaceView, MirrorController, MirrorPlugin} from "@swim/prism";
import type {ReflectionControllerObserver} from "./ReflectionControllerObserver";

export class ReflectionController extends CompositeController {
  constructor(plugin: MirrorPlugin) {
    super();
    Object.defineProperty(this, "plugin", {
      value: plugin,
      enumerable: true,
    });
    this.initReflection();
  }

  protected initReflection(): void {
    this.mirror.injectController();
  }

  readonly plugin!: MirrorPlugin;

  override readonly controllerObservers!: ReadonlyArray<ReflectionControllerObserver>;

  protected initMirror(mirrorController: MirrorController): void {
    const domainGroup = this.domains.model;
    if (domainGroup !== null) {
      mirrorController.domains.setModel(domainGroup);
    }
  }

  protected initRowView(rowView: RowView): void {
    const iconCellView = rowView.getOrCreateCell("icon", IconCellView)
        .iconWidth(24, View.Intrinsic)
        .iconHeight(24, View.Intrinsic);
    const titleCellView = rowView.getOrCreateCell("title", TextCellView);

    iconCellView.graphics(this.plugin.icon);
    titleCellView.content(this.plugin.title);
  }

  protected attachSurface(surfaceView: SurfaceView): void {
    const mirrorController = this.mirror.controller;
    if (mirrorController !== null) {
      surfaceView.setFullBleed(mirrorController.isFullBleed());
      mirrorController.surface.setView(surfaceView);
    }
  }

  protected detachSurface(surfaceView: SurfaceView): void {
    const mirrorController = this.mirror.controller;
    if (mirrorController !== null) {
      mirrorController.surface.setView(null);
    }
  }

  protected attachToolbar(toolbarView: HtmlView): void {
    const mirrorController = this.mirror.controller;
    if (mirrorController !== null) {
      mirrorController.toolbar.setView(toolbarView);
    }
  }

  protected detachToolbar(toolbarView: HtmlView): void {
    const mirrorController = this.mirror.controller;
    if (mirrorController !== null) {
      mirrorController.toolbar.setView(null);
    }
  }

  protected attachDrawer(drawerView: DrawerView): void {
    const mirrorController = this.mirror.controller;
    if (mirrorController !== null) {
      mirrorController.drawer.setView(drawerView);
    }
  }

  protected detachDrawer(drawerView: DrawerView): void {
    const mirrorController = this.mirror.controller;
    if (mirrorController !== null) {
      mirrorController.drawer.setView(null);
    }
  }

  activate(): void {
    this.willActivate();
    this.onActivate();
    this.didActivate();
  }

  protected willActivate(): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerWillActivateReflection !== void 0) {
        controllerObserver.controllerWillActivateReflection(this);
      }
    }
  }

  protected onActivate(): void {
    // hook
  }

  protected didActivate(): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerDidActivateReflection !== void 0) {
        controllerObserver.controllerDidActivateReflection(this);
      }
    }
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const mirrorController = this.mirror.controller;
    if (mirrorController !== null) {
      mirrorController.domains.setModel(domainGroup);
    }
  }

  @ControllerFastener<ReflectionController, MirrorController>({
    type: MirrorController,
    onSetController(mirrorController: MirrorController | null): void {
      if (mirrorController !== null) {
        this.owner.initMirror(mirrorController);
      }
    },
    createController(): MirrorController | null {
      return this.owner.plugin.createController();
    },
  })
  readonly mirror!: ControllerFastener<this, MirrorController>;

  @ControllerView<ReflectionController, RowView>({
    type: RowView,
    observe: true,
    onSetView(rowView: RowView | null): void {
      if (rowView !== null) {
        this.owner.initRowView(rowView);
      }
    },
    viewDidPressLeaf(input: PositionGestureInput, event: Event | null,
                     leafView: LeafView, rowView: RowView): void {
      rowView.modalService.dismissModals();
      this.owner.activate();
    },
  })
  readonly row!: ControllerView<this, RowView>;

  @ControllerView<ReflectionController, SurfaceView>({
    type: SurfaceView,
    onSetView(newSurfaceView: SurfaceView | null, oldSurfaceView: SurfaceView | null): void {
      if (oldSurfaceView !== null) {
        this.owner.detachSurface(oldSurfaceView);
      }
      if (newSurfaceView !== null) {
        this.owner.attachSurface(newSurfaceView);
      }
    },
  })
  readonly surface!: ControllerView<this, SurfaceView>;

  @ControllerView<ReflectionController, HtmlView>({
    type: HtmlView,
    onSetView(newToolbarView: HtmlView | null, oldToolbarView: HtmlView | null): void {
      if (oldToolbarView !== null) {
        this.owner.detachToolbar(oldToolbarView);
      }
      if (newToolbarView !== null) {
        this.owner.attachToolbar(newToolbarView);
      }
    },
  })
  readonly toolbar!: ControllerView<this, HtmlView>;

  @ControllerView<ReflectionController, DrawerView>({
    type: DrawerView,
    onSetView(newDrawerView: DrawerView | null, oldDrawerView: DrawerView | null): void {
      if (oldDrawerView !== null) {
        this.owner.detachDrawer(oldDrawerView);
      }
      if (newDrawerView !== null) {
        this.owner.attachDrawer(newDrawerView);
      }
    },
  })
  readonly drawer!: ControllerView<this, DrawerView>;

  @ControllerModel<ReflectionController, DomainGroup>({
    type: DomainGroup,
    onSetModel(domainGroup: DomainGroup | null): void {
      if (domainGroup !== null) {
        this.owner.initDomains(domainGroup);
      }
    },
  })
  readonly domains!: ControllerModel<this, DomainGroup>;
}
