import {Values} from "@swim/util";
import type {ModelContext, Model} from "@swim/model";
import {Feel} from "@swim/theme";
import {View} from "@swim/view";
import type {ViewNode} from "@swim/dom";
import {ControllerModel, ControllerView, ControllerFastener, CompositeController} from "@swim/controller";
import {ColLayout, TableLayout, RowView, ColView, HeaderView, TableView} from "@swim/table";
import {IndicatorType, EntityTrait, EntityGroup} from "@swim/domain";
import {CatalogEntityLimb} from "./CatalogEntityLimb";

export class CatalogEntityTree extends CompositeController {
  protected createTree(): TableView {
    const treeView = TableView.create();
    treeView.modifyTheme(Feel.default, [[Feel.primary, 1]], false);
    treeView.rowHeight(58)
            .rowSpacing(2)
            .layout(this.createLayout());

    const headerView = HeaderView.create();
    treeView.header.setView(headerView);
    this.updateHeader(headerView);

    return treeView;
  }

  protected initTree(treeView: TableView): void {
    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i];
      if (childController instanceof CatalogEntityLimb) {
        this.onInsertBranch(childController);
      }
    }

    const entityGroup = this.entities.model;
    if (entityGroup !== null) {
      this.onReconcileEntities(entityGroup);
    }
  }

  protected createLayout(indicatorTypes?: ReadonlyArray<IndicatorType>): TableLayout {
    const cols = new Array<ColLayout>();
    cols.push(ColLayout.create("icon", 0, 0, 64));
    cols.push(ColLayout.create("title", 1, 1, 200));
    if (indicatorTypes !== void 0) {
      for (let i = 0; i < indicatorTypes.length; i += 1) {
        const indicatorType = indicatorTypes[i]!;
        const indicatorRoot = this.createIndicatorRoot(indicatorType);
        if (indicatorRoot !== null) {
          cols.push(indicatorRoot);
        }
      }
    }
    cols.push(ColLayout.create("disclose", 0, 0, 40));
    return TableLayout.create(cols);
  }

  protected createIndicatorRoot(indicatorType: IndicatorType): ColLayout | null {
    return ColLayout.create(indicatorType.key, 0, 0, 100, true);
  }

  protected updateHeader(headerView: HeaderView, indicatorTypes?: ReadonlyArray<IndicatorType>): void {
    let nameColView = headerView.getChildView("title") as ColView | null;
    if (nameColView === null) {
      nameColView = this.createNameColView();
      if (nameColView !== null) {
        headerView.append(nameColView, "title");
      }
    }

    if (indicatorTypes !== void 0) {
      for (let i = 0; i < indicatorTypes.length; i += 1) {
        const indicatorType = indicatorTypes[i]!;
        let indicatorColView = headerView.getChildView(indicatorType.key) as ColView | null;
        if (indicatorColView === null) {
          indicatorColView = this.createIndicatorColView(indicatorType);
          if (indicatorColView !== null) {
            headerView.append(indicatorColView, indicatorType.key);
          }
        }
      }
    }
  }

  protected createNameColView(): ColView | null {
    return ColView.create().label("Name");
  }

  protected createIndicatorColView(indicatorType: IndicatorType): ColView | null {
    return ColView.create().label(indicatorType.name);
  }

  protected initEntities(entityGroup: EntityGroup): void {
    const entities = entityGroup.childModels;
    for (let i = 0, n = entities.length; i < n; i += 1) {
      const entity = entities[i]!;
      const entityTrait = entity.getTrait(EntityTrait);
      if (entityTrait !== null) {
        this.onInsertEntity(entityTrait, null);
      }
    }
    this.onReconcileEntities(entityGroup);
  }

  protected onInsertEntity(childEntity: EntityTrait, targetEntity: EntityTrait | null): void {
    const id = childEntity.uri.toString();
    let controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, CatalogEntityLimb> | null;
    if (controllerFastener === null) {
      controllerFastener = new CatalogEntityTree.BranchFastener(this, id, "branch") as ControllerFastener<this, CatalogEntityLimb>;
      this.setControllerFastener(id, controllerFastener);
      const branchController = new CatalogEntityLimb();
      branchController.entity.setTrait(childEntity);
      const targetController = targetEntity !== null ? this.getChildController(targetEntity.uri.toString()) : null;
      this.insertChildController(branchController, targetController, id);
    }
  }

  protected onRemoveEntity(childEntity: EntityTrait): void {
    const id = childEntity.uri.toString();
    const controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, CatalogEntityLimb> | null;
    if (controllerFastener !== null) {
      controllerFastener.removeController();
      this.setControllerFastener(id, null);
    }
  }

  protected onInsertBranch(branchController: CatalogEntityLimb): void {
    const treeView = this.tree.view;
    if (treeView !== null) {
      const id = branchController.entity.trait!.uri.toString();
      let rowView: RowView | null;
      if (this.entities.model!.isSorted()) {
        let index = this.lookupBranch(treeView, id);
        if (index >= 0) {
          rowView = (treeView.node.childNodes[index] as ViewNode).view as RowView;
        } else {
          index = -(index + 1);
          rowView = branchController.row.createView();
          if (rowView !== null) {
            const targetNode = treeView.node.childNodes[index] as ViewNode | undefined;
            const targetView = targetNode !== void 0 ? targetNode.view : void 0;
            treeView.insertChildView(rowView, targetView !== void 0 ? targetView : null, id);
          }
        }
        branchController.row.setView(rowView);
      } else {
        rowView = branchController.row.createView();
        if (rowView !== null) {
          const targetBranch = this.nextChildController(branchController);
          const targetView = targetBranch instanceof CatalogEntityLimb ? targetBranch.row.view : null;
          treeView.insertChildView(rowView, targetView, id);
          branchController.row.setView(rowView);
        }
      }
    }
  }

  protected onRemoveBranch(branchController: CatalogEntityLimb): void {
    const rowView = branchController.row.view;
    if (rowView !== null) {
      rowView.remove();
    }
  }

  protected lookupBranch(treeView: TableView, key: unknown): number {
    const childNodes = treeView.node.childNodes;
    let lo = 0;
    let hi = childNodes.length - 1;
    while (lo <= hi && !((childNodes[lo] as ViewNode).view instanceof RowView)) {
      lo += 1;
    }
    while (hi >= lo && !((childNodes[hi] as ViewNode).view instanceof RowView)) {
      hi -= 1;
    }
    while (lo <= hi) {
      const mid = (lo + hi) >>> 1;
      const order = Values.compare(key, (childNodes[mid] as ViewNode).view!.key);
      if (order > 0) {
        lo = mid + 1;
      } else if (order < 0) {
        hi = mid - 1;
      } else {
        return mid;
      }
    }
    return -(lo + 1);
  }

  protected onReconcileEntities(entityGroup: EntityGroup): void {
    const treeView = this.tree.view;
    if (treeView !== null) {
      const indicatorTypes = entityGroup.indicatorMap.indicatorTypes;
      const oldLayout = treeView.layout.state;
      const newLayout = this.createLayout(indicatorTypes);
      if (oldLayout === null || !oldLayout.equivalentTo(newLayout)) {
        treeView.layout.setState(newLayout);
        const headerView = treeView.header.view;
        if (headerView !== null) {
          this.updateHeader(headerView, indicatorTypes);
        }
        treeView.requireUpdate(View.NeedsResize | View.NeedsLayout);
      }
    }
  }

  @ControllerView<CatalogEntityTree, TableView>({
    key: "tree",
    type: TableView,
    onSetView(treeView: TableView | null): void {
      if (treeView !== null) {
        this.owner.initTree(treeView);
      }
    },
    createView(): TableView | null {
      return this.owner.createTree();
    },
  })
  readonly tree!: ControllerView<this, TableView>;

  static BranchFastener = ControllerFastener.define<CatalogEntityTree, CatalogEntityLimb>({
    type: CatalogEntityLimb,
    onSetController(newBranch: CatalogEntityLimb | null, oldBranch: CatalogEntityLimb | null): void {
      if (oldBranch !== null) {
        this.owner.onRemoveBranch(oldBranch);
      }
      if (newBranch !== null) {
        this.owner.onInsertBranch(newBranch);
      }
    },
  });

  @ControllerModel<CatalogEntityTree, EntityGroup>({
    type: EntityGroup,
    observe: true,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initEntities(entityGroup);
      }
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        const targetEntityTrait = targetModel !== null ? targetModel.getTrait(EntityTrait) : null;
        this.owner.onInsertEntity(childEntityTrait, targetEntityTrait);
      }
    },
    modelWillRemoveChildModel(childModel: Model, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        this.owner.onRemoveEntity(childEntityTrait);
      }
    },
    modelDidReconcile(modelContext: ModelContext, entityGroup: EntityGroup): void {
      this.owner.onReconcileEntities(entityGroup);
    },
  })
  readonly entities!: ControllerModel<this, EntityGroup>;
}
