import type {Trait} from "@swim/model";
import {HtmlView} from "@swim/dom";
import {
  Controller,
  ControllerService,
  ControllerFastener,
  ControllerTrait,
  ControllerView,
  CompositeController,
} from "@swim/controller";
import {EntityTrait} from "@swim/domain";
import {WidgetTrait, WidgetGroup} from "@swim/widget";
import {PrismManager, PrismService} from "@swim/prism";
import {RefractionController} from "./RefractionController";

export class RefractorController extends CompositeController {
  protected attachEntity(entityTrait: EntityTrait): void {
    const widgetGroup = entityTrait.getTrait(WidgetGroup);
    if (widgetGroup !== null) {
      this.widgets.setTrait(widgetGroup);
    }
  }

  protected detachEntity(entityTrait: EntityTrait): void {
    this.widgets.setTrait(null);
    const childControllers = this.childControllers;
    for (let i = 0, n = childControllers.length; i < n; i += 1) {
      const childController = childControllers[i]!;
      if (childController instanceof RefractionController) {
        this.setControllerFastener(childController.key!, null);
      }
    }
    this.removeAll();
    const fixturesView = this.fixtures.view;
    if (fixturesView !== null) {
      fixturesView.removeAll();
    }
  }

  protected initFixtures(fixturesView: HtmlView): void {
    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i];
      if (childController instanceof RefractionController) {
        this.onInsertRefraction(childController);
      }
    }
  }

  protected initWidgets(widgetGroup: WidgetGroup): void {
    const widgetFasteners = widgetGroup.widgetFasteners;
    for (let i = 0, n = widgetFasteners.length; i < n; i += 1) {
      const widgetTrait = widgetFasteners[i]!.trait;
      if (widgetTrait !== null) {
        this.onInsertWidget(widgetTrait, null);
      }
    }
  }

  protected createRefraction(widgetTrait: WidgetTrait): RefractionController | null {
    const entityTrait = this.entity.trait;
    const refractionController = new RefractionController();
    refractionController.entity.setTrait(entityTrait);
    refractionController.widget.setTrait(widgetTrait);
    return refractionController;
  }

  protected onInsertWidget(childWidgetTrait: WidgetTrait, targetTrait: Trait | null): void {
    const id = childWidgetTrait.model!.key!;
    let refractionFastener = this.getControllerFastener(id) as ControllerFastener<this, RefractionController> | null;
    if (refractionFastener === null) {
      refractionFastener = new RefractorController.RefractionFastener(this, id, "refraction");
      this.setControllerFastener(id, refractionFastener);
      const refractionController = this.createRefraction(childWidgetTrait);
      if (refractionController !== null) {
        let targetController: Controller | null = null;
        if (targetTrait !== null) {
          targetController = this.getChildController(targetTrait.model!.key!);
        }
        this.insertChildController(refractionController, targetController, id);
      }
    }
  }

  protected onRemoveWidget(childWidgetTrait: WidgetTrait): void {
    const id = childWidgetTrait.model!.key!;
    const refractionFastener = this.getControllerFastener(id) as ControllerFastener<this, RefractionController> | null;
    if (refractionFastener !== null) {
      refractionFastener.removeController();
      this.setControllerFastener(id, null);
    }
  }

  protected onInsertRefraction(refractionController: RefractionController): void {
    const targetController = this.nextChildController(refractionController);
    const widgetTrait = refractionController.widget.trait!;
    const id = widgetTrait.model!.key;

    const fixturesView = this.fixtures.view;
    if (fixturesView !== null) {
      const fixtureView = refractionController.widget.createView();
      if (fixtureView !== null) {
        const targetView = targetController instanceof RefractionController ? targetController.widget.view : null;
        fixturesView.insertChildView(fixtureView, targetView, id);
        refractionController.widget.setView(fixtureView);
      }
    }
  }

  protected onRemoveRefraction(refractionController: RefractionController): void {
    refractionController.widget.removeView();
  }

  static RefractionFastener = ControllerFastener.define<RefractorController, RefractionController>({
    type: RefractionController,
    onSetController(newRefractionController: RefractionController | null,
                    oldRefractionController: RefractionController | null): void {
      if (oldRefractionController !== null) {
        this.owner.onRemoveRefraction(oldRefractionController);
      }
      if (newRefractionController !== null) {
        this.owner.onInsertRefraction(newRefractionController);
      }
    },
  });

  @ControllerView<RefractorController, HtmlView>({
    type: HtmlView,
    onSetView(fixturesView: HtmlView | null): void {
      if (fixturesView !== null) {
        this.owner.initFixtures(fixturesView);
      }
    },
  })
  readonly fixtures!: ControllerView<this, HtmlView>;

  @ControllerTrait<RefractorController, EntityTrait>({
    type: EntityTrait,
    onSetTrait(newEntityTrait: EntityTrait | null, oldEntityTrait: EntityTrait | null): void {
      if (oldEntityTrait !== null) {
        this.owner.detachEntity(oldEntityTrait);
      }
      if (newEntityTrait !== null) {
        this.owner.attachEntity(newEntityTrait);
      }
    },
  })
  readonly entity!: ControllerTrait<this, EntityTrait>;

  @ControllerTrait<RefractorController, WidgetGroup>({
    type: WidgetGroup,
    observe: true,
    onSetTrait(widgetGroup: WidgetGroup | null): void {
      if (widgetGroup !== null) {
        this.owner.initWidgets(widgetGroup);
      }
    },
    traitDidSetWidget(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null, targetTrait: Trait | null, widgetGroup: WidgetGroup): void {
      if (oldWidgetTrait !== null) {
        this.owner.onRemoveWidget(oldWidgetTrait);
      }
      if (newWidgetTrait !== null) {
        this.owner.onInsertWidget(newWidgetTrait, targetTrait);
      }
    },
  })
  readonly widgets!: ControllerTrait<this, WidgetGroup>;

  @ControllerService<RefractorController, PrismManager>({
    type: PrismManager,
    extends: PrismService,
    observe: false,
  })
  readonly prismService!: PrismService<this>;

  protected override onUnmount(): void {
    super.onUnmount();
    this.entity.setTrait(null);
  }
}
