import type {Model, Trait} from "@swim/model";
import {View} from "@swim/view";
import {Controller, ControllerTrait} from "@swim/controller";
import {TableTrait} from "@swim/table";
import {GaugeTrait} from "@swim/gauge";
import {PieTrait} from "@swim/pie";
import {ChartTrait} from "@swim/chart";
import {EntityTrait} from "@swim/domain";
import {
  GadgetController,
  WidgetView,
  WidgetController,
  TableGadgetController,
  GaugeGadgetController,
  PieGadgetController,
  ChartGadgetController,
} from "@swim/widget";
import type {RefractionControllerObserver} from "./RefractionControllerObserver";

export class RefractionController extends WidgetController {
  override readonly controllerObservers!: ReadonlyArray<RefractionControllerObserver>;

  protected override initWidgetView(widgetView: WidgetView): void {
    super.initWidgetView(widgetView);
    widgetView.marginTop.setState(4, View.Intrinsic);
    widgetView.marginRight.setState(8, View.Intrinsic);
    widgetView.marginBottom.setState(8, View.Intrinsic);
    widgetView.marginLeft.setState(8, View.Intrinsic);
  }

  protected override createGadget(gadgetTrait: Trait): GadgetController | null {
    if (gadgetTrait instanceof TableTrait) {
      return new TableGadgetController();
    } else if (gadgetTrait instanceof GaugeTrait) {
      return new GaugeGadgetController();
    } else if (gadgetTrait instanceof PieTrait) {
      return new PieGadgetController();
    } else if (gadgetTrait instanceof ChartTrait) {
      return new ChartGadgetController();
    }
    return null;
  }

  protected override detectGadgetModel(model: Model): Trait | null {
    let gadgetTrait = null;
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(TableTrait);
    }
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(GaugeTrait);
    }
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(PieTrait);
    }
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(ChartTrait);
    }
    return gadgetTrait;
  }

  protected override detectGadgetController(controller: Controller): GadgetController | null {
    if (controller instanceof TableGadgetController ||
        controller instanceof GaugeGadgetController ||
        controller instanceof PieGadgetController ||
        controller instanceof ChartGadgetController) {
      return controller;
    }
    return null;
  }

  protected initEntity(entityTrait: EntityTrait): void {
    // hook
  }

  @ControllerTrait<RefractionController, EntityTrait>({
    type: EntityTrait,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
  })
  readonly entity!: ControllerTrait<this, EntityTrait>;

  protected override onUnmount(): void {
    super.onUnmount();
    this.widget.removeView();
  }
}
