import {Lazy} from "@swim/util";
import type {Model} from "@swim/model";
import {Feel} from "@swim/theme";
import {HtmlView} from "@swim/dom";
import {Graphics, VectorIcon} from "@swim/graphics";
import {ControllerModel, ControllerTrait, ControllerView, ControllerFastener} from "@swim/controller";
import {IconButton} from "@swim/button";
import {ColLayout, TableLayout, ColView, HeaderView, TableView} from "@swim/table";
import {EntityTrait} from "@swim/domain";
import {ActivityController} from "@swim/prism";
import type {MetaNodeEntity} from "@swim/fabric";
import {LaneModel} from "./LaneModel";
import {ManifestModel} from "./ManifestModel";
import {LaneController} from "./LaneController";
import {InventoryView} from "./InventoryView";

export class InventoryController extends ActivityController {
  constructor() {
    super();
    Object.defineProperty(this, "selectedItem", {
      value: null,
      enumerable: true,
      configurable: true,
    });
  }

  /** @hidden */
  readonly selectedItem!: LaneController | null;

  protected initRoot(rootView: InventoryView): void {
    rootView.position("absolute")
            .top(0)
            .right(0)
            .bottom(0)
            .left(0);

    this.masterTree.setView(rootView.masterTree.view);
    this.detail.setView(rootView.detail.view);
    this.closeButton.injectView(rootView);
  }

  protected initMasterTree(masterTreeView: TableView): void {
    masterTreeView.modifyTheme(Feel.default, [[Feel.primary, 1], [Feel.transparent, 1], [Feel.unselected, 1]], false);
    masterTreeView.rowHeight.setState(44);
    masterTreeView.layout.setState(this.createMasterLayout());

    const headerView = HeaderView.create();
    masterTreeView.header.setView(headerView);
    this.updateMasterHeader(headerView);

    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i]!;
      if (childController instanceof LaneController) {
        this.onInsertItem(childController);
      }
    }
  }

  protected initDetail(detailView: HtmlView): void {
    // hook
  }

  protected createMasterLayout(): TableLayout {
    return TableLayout.create([
      ColLayout.create("icon", 0, 0, 64),
      ColLayout.create("name", 1, 1, 200),
      ColLayout.create("kind", 0, 0, 64),
    ]);
  }

  protected updateMasterHeader(headerView: HeaderView): void {
    let nameColView = headerView.getChildView("name") as ColView | null;
    if (nameColView === null) {
      nameColView = ColView.create().label("Name");
      headerView.append(nameColView, "name");
    }
    let kindColView = headerView.getChildView("kind") as ColView | null;
    if (kindColView === null) {
      kindColView = ColView.create().label("Kind");
      headerView.append(kindColView, "kind");
    }
  }

  protected initCloseButton(closeButton: IconButton): void {
    closeButton.position("absolute")
               .top(0)
               .left(0)
               .width(InventoryView.BarHeight)
               .height(InventoryView.BarHeight);
    closeButton.pushIcon(InventoryController.closeIcon);
  }

  protected didPressCloseButton(): void {
    const windowView = this.window.view;
    if (windowView !== null) {
      windowView.modalService.dismissModals();
    }
  }

  protected attachWindow(windowView: HtmlView): void {
    this.root.injectView(windowView);
  }

  protected detachWindow(windowView: HtmlView): void {
    windowView.removeAll();
    const manifestModel = this.manifest.model;
    if (manifestModel !== null) {
      manifestModel.remove();
    }
  }

  protected attachEntity(entityTrait: EntityTrait): void {
    const model = entityTrait.model;
    if (model !== null) {
      this.manifest.injectModel(model);
    }
  }

  protected detachEntity(entityTrait: EntityTrait): void {
    this.manifest.removeModel();
  }

  protected initManifest(manifestModel: ManifestModel): void {
    // hook
  }

  protected onInsertLane(childEntity: LaneModel, targetEntity: LaneModel | null): void {
    const id = childEntity.ownLaneUri.toString();
    let controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, LaneController> | null;
    if (controllerFastener === null) {
      controllerFastener = new InventoryController.ItemFastener(this, id, "item") as ControllerFastener<this, LaneController>;
      this.setControllerFastener(id, controllerFastener);
      const itemController = new LaneController();
      itemController.lane.setModel(childEntity);
      const targetController = targetEntity !== null ? this.getChildController(targetEntity.ownLaneUri.toString()) : null;
      this.insertChildController(itemController, targetController, id);
    }
  }

  protected onRemoveLane(childEntity: LaneModel): void {
    const id = childEntity.ownLaneUri.toString();
    const controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, LaneController> | null;
    if (controllerFastener !== null) {
      controllerFastener.removeController();
      this.setControllerFastener(id, null);
    }
  }

  protected onInsertItem(item: LaneController): void {
    const masterTreeView = this.masterTree.view;
    if (masterTreeView !== null) {
      const id = item.lane.model!.ownLaneUri.toString();
      const rowView = item.row.createView();
      if (rowView !== null) {
        const targetItem = this.nextChildController(item);
        const targetView = targetItem instanceof LaneController ? targetItem.row.view : null;
        masterTreeView.insertChildView(rowView, targetView, id);
        item.row.setView(rowView);
      }
    }
  }

  protected onRemoveItem(item: LaneController): void {
    const rowView = item.row.view;
    if (rowView !== null) {
      rowView.remove();
    }
  }

  protected onSelectItem(newItem: LaneController): void {
    const oldItem = this.selectedItem;
    if (oldItem !== null) {
      oldItem.unselect();
      oldItem.detail.setView(null);
    }
    const detailView = this.detail.view;
    if (detailView !== null) {
      detailView.removeAll();
      Object.defineProperty(this, "selectedItem", {
        value: newItem,
        enumerable: true,
        configurable: true,
      });
      newItem.select();
      newItem.detail.setView(this.detail.view);
    }
  }

  @ControllerView<InventoryController, InventoryView>({
    type: InventoryView,
    onSetView(rootView: InventoryView | null): void {
      if (rootView !== null) {
        this.owner.initRoot(rootView);
      }
    },
  })
  readonly root!: ControllerView<this, InventoryView>;

  @ControllerView<InventoryController, TableView>({
    type: TableView,
    onSetView(masterTreeView: TableView | null): void {
      if (masterTreeView !== null) {
        this.owner.initMasterTree(masterTreeView);
      }
    },
  })
  readonly masterTree!: ControllerView<this, TableView>;

  @ControllerView<InventoryController, HtmlView>({
    type: HtmlView,
    onSetView(detailView: HtmlView | null): void {
      if (detailView !== null) {
        this.owner.initDetail(detailView);
      }
    },
  })
  readonly detail!: ControllerView<this, HtmlView>;

  @ControllerView<InventoryController, IconButton>({
    type: IconButton,
    observe: true,
    onSetView(closeButton: IconButton | null): void {
      if (closeButton !== null) {
        this.owner.initCloseButton(closeButton);
      }
    },
    buttonDidPress(closeButton: IconButton): void {
      this.owner.didPressCloseButton();
    },
  })
  readonly closeButton!: ControllerView<this, IconButton>;

  static ItemFastener = ControllerFastener.define<InventoryController, LaneController>({
    type: LaneController,
    observe: true,
    onSetController(newItem: LaneController | null, oldItem: LaneController | null): void {
      if (oldItem !== null) {
        this.owner.onRemoveItem(oldItem);
      }
      if (newItem !== null) {
        this.owner.onInsertItem(newItem);
      }
    },
    controllerDidActivateLane(item: LaneController): void {
      this.owner.onSelectItem(item);
    },
  });

  @ControllerView<InventoryController, HtmlView>({
    type: HtmlView,
    onSetView(newWindowView: HtmlView | null, oldWindowView: HtmlView | null): void {
      if (oldWindowView !== null) {
        this.owner.detachWindow(oldWindowView);
      }
      if (newWindowView !== null) {
        this.owner.attachWindow(newWindowView);
      }
    },
  })
  override readonly window!: ControllerView<this, HtmlView>;

  @ControllerTrait<InventoryController, EntityTrait>({
    type: EntityTrait,
    onSetTrait(newEntityTrait: EntityTrait | null, oldEntityTrait: EntityTrait | null): void {
      if (oldEntityTrait !== null) {
        this.owner.detachEntity(oldEntityTrait);
      }
      if (newEntityTrait !== null) {
        this.owner.attachEntity(newEntityTrait);
      }
    },
  })
  override readonly entity!: ControllerTrait<this, EntityTrait>;

  @ControllerModel<InventoryController, ManifestModel>({
    type: ManifestModel,
    observe: true,
    onSetModel(manifestModel: ManifestModel | null): void {
      if (manifestModel !== null) {
        this.owner.initManifest(manifestModel);
      }
    },
    createModel(): ManifestModel | null {
      const entityTrait = this.owner.entity.trait! as MetaNodeEntity;
      return new ManifestModel(entityTrait.metaNodeUri);
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null): void {
      if (childModel instanceof LaneModel) {
        this.owner.onInsertLane(childModel, targetModel instanceof LaneModel ? targetModel : null);
      }
    },
    modelWillRemoveChildModel(childModel: Model): void {
      if (childModel instanceof LaneModel) {
        this.owner.onRemoveLane(childModel);
      }
    },
  })
  readonly manifest!: ControllerModel<this, ManifestModel>;

  @Lazy
  static get closeIcon(): Graphics {
    return VectorIcon.create(24, 24, "M19,6.4L17.6,5L12,10.6L6.4,5L5,6.4L10.6,12L5,17.6L6.4,19L12,13.4L17.6,19L19,17.6L13.4,12Z");
  }
}
