import type {ModelContext, Model} from "@swim/model";
import {ControllerModel, ControllerView, ControllerFastener, CompositeController} from "@swim/controller";
import {GeoView, GeoTreeView} from "@swim/map";
import {EntityTrait, EntityGroup} from "@swim/domain";
import {AtlasEntityLocation} from "./AtlasEntityLocation";

export class AtlasEntityDistrict extends CompositeController {
  protected initLayer(layerView: GeoView): void {
    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i];
      if (childController instanceof AtlasEntityLocation) {
        this.onInsertLocation(childController);
      }
    }

    const entityGroup = this.entities.model;
    if (entityGroup !== null) {
      this.onAggregateEntities(entityGroup);
    }
  }

  protected initEntities(entityGroup: EntityGroup): void {
    const entities = entityGroup.childModels;
    for (let i = 0, n = entities.length; i < n; i += 1) {
      const entity = entities[i]!;
      const entityTrait = entity.getTrait(EntityTrait);
      if (entityTrait !== null) {
        this.onInsertEntity(entityTrait, null);
      }
    }
    this.onAggregateEntities(entityGroup);
  }

  protected onInsertEntity(childEntity: EntityTrait, targetEntity: EntityTrait | null): void {
    const id = childEntity.uri.toString();
    let controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, AtlasEntityLocation> | null;
    if (controllerFastener === null) {
      controllerFastener = new AtlasEntityDistrict.LocationFastener(this, id, "location") as ControllerFastener<this, AtlasEntityLocation>;
      this.setControllerFastener(id, controllerFastener);
      const locationController = new AtlasEntityLocation();
      locationController.entity.setTrait(childEntity);
      const targetController = targetEntity !== null ? this.getChildController(targetEntity.uri.toString()) : null;
      this.insertChildController(locationController, targetController, id);
    }
  }

  protected onRemoveEntity(childEntity: EntityTrait): void {
    const id = childEntity.uri.toString();
    const controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, AtlasEntityLocation> | null;
    if (controllerFastener !== null) {
      controllerFastener.removeController();
      this.setControllerFastener(id, null);
    }
  }

  protected onInsertLocation(locationController: AtlasEntityLocation): void {
    const layerView = this.layer.view;
    if (layerView !== null) {
      locationController.layer.setView(layerView);
    }
  }

  protected onRemoveLocation(locationController: AtlasEntityLocation): void {
    // hook
  }

  protected onAggregateEntities(entityGroup: EntityGroup): void {
    // hook
  }

  @ControllerView<AtlasEntityDistrict, GeoView>({
    onSetView(layerView: GeoView | null): void {
      if (layerView !== null) {
        this.owner.initLayer(layerView);
      }
    },
    createView(): GeoView | null {
      return GeoTreeView.create();
    },
  })
  readonly layer!: ControllerView<this, GeoView>;

  static LocationFastener = ControllerFastener.define<AtlasEntityDistrict, AtlasEntityLocation>({
    type: AtlasEntityLocation,
    onSetController(newLocation: AtlasEntityLocation | null, oldLocation: AtlasEntityLocation | null): void {
      if (oldLocation !== null) {
        this.owner.onRemoveLocation(oldLocation);
      }
      if (newLocation !== null) {
        this.owner.onInsertLocation(newLocation);
      }
    },
  });

  @ControllerModel<AtlasEntityDistrict, EntityGroup>({
    type: EntityGroup,
    observe: true,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initEntities(entityGroup);
      }
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        const targetEntityTrait = targetModel !== null ? targetModel.getTrait(EntityTrait) : null;
        this.owner.onInsertEntity(childEntityTrait, targetEntityTrait);
      }
    },
    modelWillRemoveChildModel(childModel: Model, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        this.owner.onRemoveEntity(childEntityTrait);
      }
    },
    modelDidAggregate(modelContext: ModelContext, entityGroup: EntityGroup): void {
      this.owner.onAggregateEntities(entityGroup);
    },
  })
  readonly entities!: ControllerModel<this, EntityGroup>;
}
