import {Lazy} from "@swim/util";
import type {Timing} from "@swim/mapping";
import type {Model} from "@swim/model";
import {Look, Feel, MoodVector, ThemeMatrix} from "@swim/theme";
import {View} from "@swim/view";
import type {HtmlView} from "@swim/dom";
import {Graphics, VectorIcon} from "@swim/graphics";
import {
  ControllerService,
  ControllerProperty,
  ControllerModel,
  ControllerView,
  ControllerFastener,
  CompositeController,
} from "@swim/controller";
import {InputTokenView} from "@swim/token";
import {TableView} from "@swim/table";
import {PopoverView} from "@swim/window";
import {EntityTrait, DomainTrait, DomainGroup} from "@swim/domain";
import {PrismManager, PrismService, DomainPlugin} from "@swim/prism";
import {SuggestionController} from "./SuggestionController";
import {CollectionController} from "./CollectionController";

export class CollectorController extends CompositeController {
  get searchIcon(): Graphics {
    return CollectorController.searchIcon;
  }

  get plusIcon(): Graphics {
    return CollectorController.plusIcon;
  }

  protected initSearch(searchView: InputTokenView): void {
    searchView.icon.setState(this.searchIcon);
    searchView.icon.embossed = false;

    const inputView = searchView.label.view;
    if (inputView !== null) {
      inputView.width.setState(200, View.Intrinsic);
      inputView.placeholder.setState("Search or enter a warp URL", View.Intrinsic);
    }

    searchView.accessory.setState(this.plusIcon);
    searchView.accessory.embossed = false;

    this.setFullBleed(this.fullBleed.state);
  }

  protected setFullBleed(fullBleed: boolean): void {
    const searchView = this.search.view;
    if (searchView !== null) {
      searchView.modifyMood(Feel.default, [[Feel.translucent, fullBleed ? 1 : 0]]);
    }
  }

  protected initSearchDropdown(dropdownView: PopoverView): void {
    dropdownView.addClass("search-dropdown")
                .dropdown(true)
                .placementGap(2)
                .minHeight(64)
                .maxHeight(320)
                .borderRadius(14)
                .boxSizing("content-box")
                .overflowY("auto");

    dropdownView.modifyMood(Feel.default, [[Feel.translucent, 1]]);

    this.suggestions.injectView(dropdownView);
  }

  protected initSuggestions(suggestionsList: TableView): void {
    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i];
      if (childController instanceof SuggestionController) {
        this.onInsertSuggestion(childController);
      }
    }
  }

  insertSuggestedEntity(childEntity: EntityTrait, targetEntity: EntityTrait | null): void {
    const id = childEntity.uri.toString();
    let controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, SuggestionController> | null;
    if (controllerFastener === null) {
      controllerFastener = new CollectorController.SuggestionFastener(this, id, "suggestion");
      this.setControllerFastener(id, controllerFastener);
      const suggestion = new SuggestionController();
      suggestion.entity.setTrait(childEntity);
      const targetController = targetEntity !== null ? this.getChildController(targetEntity.uri.toString()) : null;
      this.insertChildController(suggestion, targetController, id);
    }
  }

  removeSuggestedEntity(childEntity: EntityTrait): void {
    const id = childEntity.uri.toString();
    const controllerFastener = this.getControllerFastener(id) as ControllerFastener<this, SuggestionController> | null;
    if (controllerFastener !== null) {
      controllerFastener.removeController();
      this.setControllerFastener(id, null);
    }
  }

  protected onInsertSuggestion(suggestion: SuggestionController): void {
    const suggestionsList = this.suggestions.view;
    if (suggestionsList !== null) {
      const rowView = suggestion.row.createView();
      if (rowView !== null) {
        const targetSuggestion = this.nextChildController(suggestion);
        const targetView = targetSuggestion instanceof SuggestionController ? targetSuggestion.row.view : null;
        const id = suggestion.entity.trait!.uri.toString();
        suggestionsList.insertChildView(rowView, targetView, id);
        suggestion.row.setView(rowView);
      }
    }
  }

  protected onRemoveSuggestion(suggestion: SuggestionController): void {
    const rowView = suggestion.row.view;
    if (rowView !== null) {
      rowView.remove();
    }
  }

  protected initDomainList(tableView: TableView): void {
    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i];
      if (childController instanceof CollectionController) {
        this.onInsertCollection(childController);
      }
    }
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const domains = domainGroup.childModels;
    for (let i = 0, n = domains.length; i < n; i += 1) {
      const domain = domains[i]!;
      const domainTrait = domain.getTrait(DomainTrait);
      if (domainTrait !== null) {
        this.onInsertDomain(domainTrait, null);
      }
    }
  }

  protected onInsertDomain(childDomain: DomainTrait, targetDomain: DomainTrait | null): void {
    const childEntity = childDomain.getTrait(EntityTrait)!;
    const id = childEntity.uri.toString();
    let collectionFastener = this.getControllerFastener(id) as ControllerFastener<this, CollectionController> | null;
    if (collectionFastener === null) {
      collectionFastener = new CollectorController.CollectionFastener(this, id, "collection");
      this.setControllerFastener(id, collectionFastener);
      const collectionController = new CollectionController();
      collectionController.domain.setTrait(childDomain);
      const targetEntity = targetDomain !== null ? targetDomain.getTrait(EntityTrait) : null;
      const targetController = targetEntity !== null ? this.getChildController(targetEntity.uri.toString()) : null;
      this.insertChildController(collectionController, targetController, id);
    }
  }

  protected onRemoveDomain(childDomain: DomainTrait): void {
    const childEntity = childDomain.getTrait(EntityTrait)!;
    const id = childEntity.uri.toString();
    const collectionFastener = this.getControllerFastener(id) as ControllerFastener<this, CollectionController> | null;
    if (collectionFastener !== null) {
      collectionFastener.removeController();
      this.setControllerFastener(id, null);
    }
  }

  protected onInsertCollection(collectionController: CollectionController): void {
    const targetController = this.nextChildController(collectionController);
    const domainTrait = collectionController.domain.trait!;
    const entityTrait = domainTrait.getTrait(EntityTrait)!;
    const id = entityTrait.uri.toString();

    const domainList = this.domainList.view;
    if (domainList !== null) {
      const rowView = collectionController.row.createView();
      if (rowView !== null) {
        const targetView = targetController instanceof CollectionController ? targetController.row.view : null;
        domainList.insertChildView(rowView, targetView, id);
        collectionController.row.setView(rowView);
      }
    }
  }

  protected onRemoveCollection(collectionController: CollectionController): void {
    collectionController.row.removeView();
  }

  protected onSearchUpdate(query: string): void {
    const searchView = this.search.view!;
    let dropdownView = this.searchDropdown.view;
    if (query.length !== 0) {
      if (dropdownView === null) {
        dropdownView = this.searchDropdown.createView()!;
        this.searchDropdown.setView(dropdownView);
      }
      dropdownView.setSource(searchView);
      //searchView.modalService.presentModal(dropdownView);

      //const suggestedEntity = this.createDomain(query);
      //if (suggestedEntity !== null) {
      //  const entityTrait = suggestedEntity.getTrait(EntityTrait);
      //  if (entityTrait !== null) {
      //    this.insertSuggestedEntity(entityTrait, null);
      //  }
      //}
    } else if (dropdownView !== null) {
      searchView.modalService.dismissModal(dropdownView);
    }
  }

  protected onSearchChange(query: string): void {
    const domainGroup = this.domains.model;
    if (domainGroup !== null) {
      const domainTrait = this.createDomain(query);
      if (domainTrait !== null) {
        domainGroup.prependChildModel(domainTrait.model!);
        const searchView = this.search.view;
        if (searchView !== null) {
          const inputView = searchView.label.view;
          if (inputView !== null) {
            const inputNode = inputView.node as HTMLInputElement;
            inputNode.value = "";
            inputNode.blur();
            searchView.collapse();
          }
          const dropdownView = this.searchDropdown.view;
          if (dropdownView !== null) {
            searchView.modalService.dismissModal(dropdownView);
          }
        }
      }
    }
  }

  createDomain(query: string): DomainTrait | null {
    const plugins = this.prismService.plugins;
    for (let i = 0, n = plugins.length; i < n; i += 1) {
      const plugin = plugins[i];
      if (plugin instanceof DomainPlugin) {
        const domain = plugin.queryDomain(query);
        if (domain !== null) {
          return domain;
        }
      }
    }
    return null;
  }

  @ControllerProperty<CollectorController, boolean>({
    type: Boolean,
    inherit: true,
    didSetState(fullBleed: boolean): void {
      this.owner.setFullBleed(fullBleed);
    },
  })
  readonly fullBleed!: ControllerProperty<this, boolean>;

  @ControllerView<CollectorController, InputTokenView>({
    type: InputTokenView,
    observe: true,
    onSetView(searchView: InputTokenView | null): void {
      if (searchView !== null) {
        this.owner.initSearch(searchView);
      }
    },
    tokenDidUpdateInput(inputView: HtmlView, searchView: InputTokenView): void {
      const node = inputView.node as HTMLInputElement;
      const query = node.value;
      this.owner.onSearchUpdate(query);
    },
    tokenDidAcceptInput(inputView: HtmlView, searchView: InputTokenView): void {
      const node = inputView.node as HTMLInputElement;
      const query = node.value;
      if (query.length !== 0) {
        this.owner.onSearchChange(query);
      }
    },
  })
  readonly search!: ControllerView<this, InputTokenView>;

  @ControllerView<CollectorController, PopoverView>({
    type: PopoverView,
    observe: true,
    onSetView(dropdownView: PopoverView | null): void {
      if (dropdownView !== null) {
        this.owner.initSearchDropdown(dropdownView);
      }
    },
    viewDidApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean, dropdownView: PopoverView): void {
      dropdownView.backgroundColor.setState(theme.getOr(Look.accentColor, mood, null), View.Intrinsic);
    },
  })
  readonly searchDropdown!: ControllerView<this, PopoverView>;

  @ControllerView<CollectorController, TableView>({
    type: TableView,
    onSetView(suggestionsList: TableView | null): void {
      if (suggestionsList !== null) {
        this.owner.initSuggestions(suggestionsList);
      }
    },
  })
  readonly suggestions!: ControllerView<this, TableView>;

  static SuggestionFastener = ControllerFastener.define<CollectorController, SuggestionController>({
    type: SuggestionController,
    onSetController(newSuggestion: SuggestionController | null, oldSuggestion: SuggestionController | null): void {
      if (oldSuggestion !== null) {
        this.owner.onRemoveSuggestion(oldSuggestion);
      }
      if (newSuggestion !== null) {
        this.owner.onInsertSuggestion(newSuggestion);
      }
    },
  });

  static CollectionFastener = ControllerFastener.define<CollectorController, CollectionController>({
    type: CollectionController,
    onSetController(newCollectionController: CollectionController | null,
                    oldCollectionController: CollectionController | null): void {
      if (oldCollectionController !== null) {
        this.owner.onRemoveCollection(oldCollectionController);
      }
      if (newCollectionController !== null) {
        this.owner.onInsertCollection(newCollectionController);
      }
    },
  });

  @ControllerView<CollectorController, TableView>({
    type: TableView,
    onSetView(tableView: TableView | null): void {
      if (tableView !== null) {
        this.owner.initDomainList(tableView);
      }
    },
  })
  readonly domainList!: ControllerView<this, TableView>;

  @ControllerModel<CollectorController, DomainGroup>({
    type: DomainGroup,
    observe: true,
    onSetModel(domainGroup: DomainGroup | null): void {
      if (domainGroup !== null) {
        this.owner.initDomains(domainGroup);
      }
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null, domainGroup: DomainGroup): void {
      const childDomainTrait = childModel.getTrait(DomainTrait);
      if (childDomainTrait !== null) {
        const targetDomainTrait = targetModel !== null ? targetModel.getTrait(DomainTrait) : null;
        this.owner.onInsertDomain(childDomainTrait, targetDomainTrait);
      }
    },
    modelWillRemoveChildModel(childModel: Model, domainGroup: DomainGroup): void {
      const childDomainTrait = childModel.getTrait(DomainTrait);
      if (childDomainTrait !== null) {
        this.owner.onRemoveDomain(childDomainTrait);
      }
    },
  })
  readonly domains!: ControllerModel<this, DomainGroup>;

  @ControllerService<CollectorController, PrismManager>({
    type: PrismManager,
    extends: PrismService,
    observe: false,
  })
  readonly prismService!: PrismService<this>;

  @Lazy
  static get searchIcon(): Graphics {
    return VectorIcon.create(24, 24, "M14.375,13.25 L13.7825,13.25 L13.5725,13.0475 C14.3075,12.1925 14.75,11.0825 14.75,9.875 C14.75,7.1825 12.5675,5 9.875,5 C7.1825,5 5,7.1825 5,9.875 C5,12.5675 7.1825,14.75 9.875,14.75 C11.0825,14.75 12.1925,14.3075 13.0475,13.5725 L13.25,13.7825 L13.25,14.375 L17,18.1175 L18.1175,17 L14.375,13.25 Z M9.875,13.25 C8.0075,13.25 6.5,11.7425 6.5,9.875 C6.5,8.0075 8.0075,6.5 9.875,6.5 C11.7425,6.5 13.25,8.0075 13.25,9.875 C13.25,11.7425 11.7425,13.25 9.875,13.25 Z");
  }

  @Lazy
  static get plusIcon(): Graphics {
    return VectorIcon.create(24, 24, "M19 13h-6v6h-2v-6H5v-2h6V5h2v6h6v2z");
  }
}
