import type {Value} from "@swim/structure";
import {Recon} from "@swim/recon";
import {HtmlView} from "@swim/dom";
import {PolygonIcon} from "@swim/graphics";
import {ControllerModel, ControllerView, CompositeController} from "@swim/controller";
import {TextCellView, IconCellView, LeafView, RowView} from "@swim/table";
import {LaneModel} from "./LaneModel";
import type {LaneControllerObserver} from "./LaneControllerObserver";

export class LaneController extends CompositeController {
  override readonly controllerObservers!: ReadonlyArray<LaneControllerObserver>;

  protected initRow(rowView: RowView): void {
    this.leaf.injectView(rowView);
  }

  protected initLeaf(leafView: LeafView): void {
    this.iconCell.injectView(leafView);
    this.nameCell.injectView(leafView);
    this.kindCell.injectView(leafView);
  }

  protected createIconCell(): IconCellView {
    const iconCellView = IconCellView.create()
      .iconWidth(36)
      .graphics(PolygonIcon.create(4, Math.PI / 4));
    return iconCellView;
  }

  protected initIconCell(iconCellView: IconCellView): void {
    // hook
  }

  protected initNameCell(nameCellView: TextCellView): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      nameCellView.content(laneModel.ownLaneUri.toString());
    }
  }

  protected initKindCell(kindCellView: TextCellView): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      kindCellView.content(laneModel.info.get("laneType").stringValue(""));
    }
  }

  protected initLane(laneModel: LaneModel): void {
    const nameCellView = this.nameCell.view;
    if (nameCellView !== null) {
      nameCellView.content(laneModel.ownLaneUri.toString());
    }

    const kindCellView = this.kindCell.view;
    if (kindCellView !== null) {
      kindCellView.content(laneModel.info.get("laneType").stringValue(""));
    }
  }

  protected didPressLeaf(): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerDidActivateLane !== void 0) {
        controllerObserver.controllerDidActivateLane(this);
      }
    }
  }

  select(): void {
    const leafView = this.leaf.view;
    if (leafView !== null) {
      leafView.highlight.focus();
    }
  }

  unselect(): void {
    const leafView = this.leaf.view;
    if (leafView !== null) {
      leafView.highlight.unfocus();
    }
  }

  protected attachDetail(detailView: HtmlView | null): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      laneModel.addModelConsumer(this);
    }
  }

  protected detachDetail(detailView: HtmlView | null): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      laneModel.removeModelConsumer(this);
    }
  }

  protected onLaneEvent(value: Value): void {
    const detailView = this.detail.view;
    if (detailView !== null) {
      detailView.append("p").text(Recon.toString(value));
    }
  }

  @ControllerView<LaneController, RowView>({
    type: RowView,
    onSetView(rowView: RowView | null): void {
      if (rowView !== null) {
        this.owner.initRow(rowView);
      }
    },
  })
  readonly row!: ControllerView<this, RowView>;

  @ControllerView<LaneController, LeafView>({
    key: true,
    type: LeafView,
    observe: true,
    onSetView(leafView: LeafView | null): void {
      if (leafView !== null) {
        this.owner.initLeaf(leafView);
      }
    },
    viewDidPress(): void {
      this.owner.didPressLeaf();
    },
  })
  readonly leaf!: ControllerView<this, LeafView>;

  @ControllerView<LaneController, IconCellView>({
    key: "icon",
    type: IconCellView,
    onSetView(iconCellView: IconCellView | null): void {
      if (iconCellView !== null) {
        this.owner.initIconCell(iconCellView);
      }
    },
    createView(): IconCellView | null {
      return this.owner.createIconCell();
    },
  })
  readonly iconCell!: ControllerView<this, IconCellView>;

  @ControllerView<LaneController, TextCellView>({
    key: "name",
    type: TextCellView,
    onSetView(nameCellView: TextCellView | null): void {
      if (nameCellView !== null) {
        this.owner.initNameCell(nameCellView);
      }
    },
  })
  readonly nameCell!: ControllerView<this, TextCellView>;

  @ControllerView<LaneController, TextCellView>({
    key: "kind",
    type: TextCellView,
    onSetView(kindCellView: TextCellView | null): void {
      if (kindCellView !== null) {
        this.owner.initKindCell(kindCellView);
      }
    },
  })
  readonly kindCell!: ControllerView<this, TextCellView>;

  @ControllerView<LaneController, HtmlView>({
    key: true,
    type: HtmlView,
    onSetView(newDetailView: HtmlView | null, oldDetailView: HtmlView | null): void {
      if (oldDetailView !== null) {
        this.owner.detachDetail(oldDetailView);
      }
      if (newDetailView !== null) {
        this.owner.attachDetail(newDetailView);
      }
    },
  })
  readonly detail!: ControllerView<this, HtmlView>;

  @ControllerModel<LaneController, LaneModel>({
    type: LaneModel,
    observe: true,
    onSetModel(laneModel: LaneModel | null): void {
      if (laneModel !== null) {
        this.owner.initLane(laneModel);
      }
    },
    onLaneEvent(value: Value, laneModel: LaneModel): void {
      this.owner.onLaneEvent(value);
    },
  })
  readonly lane!: ControllerModel<this, LaneModel>;
}
