import {Trait} from "@swim/model";
import {View} from "@swim/view";
import {HtmlView} from "@swim/dom";
import {ControllerViewTrait} from "@swim/controller";
import {RowTrait, RowController, TableView, TableTrait, TableController} from "@swim/table";
import type {GadgetController} from "../gadget/GadgetController";
import {TableGadgetRowController} from "./TableGadgetRowController";
import type {TableGadgetControllerObserver} from "./TableGadgetControllerObserver";

export class TableGadgetController extends TableController implements GadgetController {
  constructor() {
    super();
    this.onGadgetScroll = this.onGadgetScroll.bind(this);
  }

  override readonly controllerObservers!: ReadonlyArray<TableGadgetControllerObserver>;

  protected override initTableView(tableView: TableView): void {
    super.initTableView(tableView);
    tableView.paddingLeft.setState(18, View.Intrinsic);
    tableView.paddingRight.setState(18, View.Intrinsic);
  }

  protected override createRow(rowTrait: RowTrait): RowController | null {
    return new TableGadgetRowController();
  }

  protected initGadgetTrait(gadgetTrait: Trait): void {
    // hook
  }

  protected attachGadgetTrait(gadgetTrait: Trait): void {
    if (gadgetTrait instanceof TableTrait) {
      this.table.setTrait(gadgetTrait);
    }
  }

  protected detachGadgetTrait(gadgetTrait: Trait): void {
    if (gadgetTrait instanceof TableTrait) {
      this.table.setTrait(null);
    }
  }

  protected willSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerWillSetGadgetTrait !== void 0) {
        controllerObserver.controllerWillSetGadgetTrait(newGadgetTrait, oldGadgetTrait, this);
      }
    }
  }

  protected onSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
    if (oldGadgetTrait !== null) {
      this.detachGadgetTrait(oldGadgetTrait);
    }
    if (newGadgetTrait !== null) {
      this.attachGadgetTrait(newGadgetTrait);
      this.initGadgetTrait(newGadgetTrait);
    }
  }

  protected didSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerDidSetGadgetTrait !== void 0) {
        controllerObserver.controllerDidSetGadgetTrait(newGadgetTrait, oldGadgetTrait, this);
      }
    }
  }

  createGadgetView(): HtmlView {
    return HtmlView.create();
  }

  protected initGadgetView(gadgetView: HtmlView): void {
    gadgetView.position.setState("relative", View.Intrinsic);
    //gadgetView.height.setState(160, View.Intrinsic);
    gadgetView.overflowX.setState("hidden", View.Intrinsic);
    gadgetView.overflowY.setState("auto", View.Intrinsic);
  }

  protected attachGadgetView(gadgetView: HtmlView): void {
    gadgetView.on("scroll", this.onGadgetScroll);
    this.table.injectView(gadgetView);
  }

  protected detachGadgetView(gadgetView: HtmlView): void {
    gadgetView.off("scroll", this.onGadgetScroll);
  }

  protected willSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerWillSetGadgetView !== void 0) {
        controllerObserver.controllerWillSetGadgetView(newGadgetView, oldGadgetView, this);
      }
    }
  }

  protected onSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
    if (oldGadgetView !== null) {
      this.detachGadgetView(oldGadgetView);
    }
    if (newGadgetView !== null) {
      this.attachGadgetView(newGadgetView);
      this.initGadgetView(newGadgetView);
    }
  }

  protected didSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
    const controllerObservers = this.controllerObservers;
    for (let i = 0, n = controllerObservers.length; i < n; i += 1) {
      const controllerObserver = controllerObservers[i]!;
      if (controllerObserver.controllerDidSetGadgetView !== void 0) {
        controllerObserver.controllerDidSetGadgetView(newGadgetView, oldGadgetView, this);
      }
    }
  }

  protected onGadgetScroll(event: Event): void {
    const gadgetView = this.gadget.view;
    if (gadgetView !== null) {
      gadgetView.requireUpdate(View.NeedsScroll);
    }
  }

  @ControllerViewTrait<TableGadgetController, HtmlView, Trait>({
    viewType: HtmlView,
    willSetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.willSetGadgetView(newGadgetView, oldGadgetView);
    },
    onSetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.onSetGadgetView(newGadgetView, oldGadgetView);
    },
    didSetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.didSetGadgetView(newGadgetView, oldGadgetView);
    },
    createView(): HtmlView | null {
      return this.owner.createGadgetView();
    },
    traitType: Trait,
    willSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.willSetGadgetTrait(newGadgetTrait, oldGadgetTrait);
    },
    onSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.onSetGadgetTrait(newGadgetTrait, oldGadgetTrait);
    },
    didSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.didSetGadgetTrait(newGadgetTrait, oldGadgetTrait);
    },
  })
  readonly gadget!: ControllerViewTrait<this, HtmlView, Trait>;
}
