import {Lazy} from "@swim/util";
import type {Model} from "@swim/model";
import {Feel} from "@swim/theme";
import {View, PositionGestureInput} from "@swim/view";
import {Graphics, VectorIcon} from "@swim/graphics";
import {
  ControllerService,
  ControllerProperty,
  ControllerTrait,
  ControllerView,
  CompositeController,
} from "@swim/controller";
import {TextCellView, IconCellView, RowView} from "@swim/table";
import {Status, StatusVector, StatusTrait, EntityTrait, DomainTrait} from "@swim/domain";
import {PrismManager, PrismService, EntityPlugin} from "@swim/prism";
import type {CollectionControllerObserver} from "./CollectionControllerObserver";

export class CollectionController extends CompositeController {
  get domainIcon(): Graphics {
    return CollectionController.domainIcon;
  }

  protected initRowView(rowView: RowView): void {
    const iconCellView = rowView.getOrCreateCell("icon", IconCellView)
        .iconWidth(24, View.Intrinsic)
        .iconHeight(24, View.Intrinsic);
    const titleCellView = rowView.getOrCreateCell("title", TextCellView);

    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      let entityIcon = entityTrait.icon;
      if (entityIcon === null) {
        entityIcon = this.domainIcon;
      }
      iconCellView.graphics(entityIcon, View.Intrinsic);
      let entityTitle = entityTrait.title;
      if (entityTitle === void 0) {
        entityTitle = "";
      }
      titleCellView.content(entityTitle);
    }
  }

  protected setFullBleed(fullBleed: boolean): void {
    // hook
  }

  protected initDomain(domainTrait: DomainTrait): void {
    this.entity.setTrait(domainTrait.getTrait(EntityTrait));
    this.status.setTrait(domainTrait.getTrait(StatusTrait));
  }

  protected initEntity(entityTrait: EntityTrait): void {
    const rowView = this.row.view;
    if (rowView !== null) {
      const iconCellView = rowView.getOrCreateCell("icon", IconCellView);
      let entityIcon = entityTrait.icon;
      if (entityIcon === null) {
        entityIcon = this.domainIcon;
      }
      iconCellView.graphics(entityIcon);

      const titleCellView = rowView.getOrCreateCell("title", TextCellView);
      let entityTitle = entityTrait.title;
      if (entityTitle === void 0) {
        entityTitle = "";
      }
      titleCellView.content(entityTitle);
    }
  }

  protected initStatus(statusTrait: StatusTrait): void {
    this.onSetStatus(statusTrait.statusVector);
  }

  protected onSetStatus(statusVector: StatusVector): void {
    const rowView = this.row.view;
    if (rowView !== null) {
      const alert = statusVector.get(Status.alert) || 0;
      const warning = statusVector.get(Status.warning) || 0;
      const inactive = statusVector.get(Status.inactive) || 0;
      if (alert !== 0) {
        rowView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
      } else if (warning !== 0) {
        rowView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
      } else {
        rowView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
      }
      if (inactive !== 0) {
        rowView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
      } else {
        rowView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
      }
    }
  }

  protected injectEntities(model: Model, domainTrait: DomainTrait): void {
    const entityTrait = model.getTrait(EntityTrait);
    if (entityTrait !== null) {
      this.injectEntity(entityTrait, domainTrait);
    }
    type self = this;
    function injectEntity(this: self, childModel: Model): void {
      this.injectEntities(childModel, domainTrait);
    }
    model.forEachChildModel(injectEntity, this);
  }

  protected injectEntity(entittTrait: EntityTrait, domainTrait: DomainTrait): void {
    const plugins = this.prismService.plugins;
    for (let i = 0, n = plugins.length; i < n; i += 1) {
      const plugin = plugins[i];
      if (plugin instanceof EntityPlugin) {
        plugin.injectEntity(entittTrait, domainTrait);
      }
    }
  }

  protected activate(): void {
    this.removeDomain();
  }

  removeDomain(): void {
    const domainTrait = this.domain.trait;
    if (domainTrait !== null) {
      domainTrait.model!.remove();
    }
  }

  override readonly controllerObservers!: ReadonlyArray<CollectionControllerObserver>;

  @ControllerProperty<CollectionController, boolean>({
    type: Boolean,
    inherit: true,
    didSetState(fullBleed: boolean): void {
      this.owner.setFullBleed(fullBleed);
    },
  })
  readonly fullBleed!: ControllerProperty<this, boolean>;

  @ControllerView<CollectionController, RowView>({
    type: RowView,
    observe: true,
    onSetView(rowView: RowView | null): void {
      if (rowView !== null) {
        this.owner.initRowView(rowView);
      }
    },
    viewDidPressLeaf(input: PositionGestureInput, event: Event | null): void {
      this.owner.activate();
    },
  })
  readonly row!: ControllerView<this, RowView>;

  @ControllerTrait<CollectionController, DomainTrait>({
    type: DomainTrait,
    observe: true,
    onSetTrait(domainTrait: DomainTrait | null): void {
      if (domainTrait !== null) {
        this.owner.initDomain(domainTrait);
      }
    },
    domainDidInjectEntity(entityTrait: EntityTrait, domainTrait: DomainTrait): void {
      this.owner.injectEntity(entityTrait, domainTrait);
    },
  })
  readonly domain!: ControllerTrait<this, DomainTrait>;

  @ControllerTrait<CollectionController, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined, entityTrait: EntityTrait): void {
      const rowView = this.owner.row.view;
      if (rowView !== null) {
        const titleCellView = rowView.getOrCreateCell("title", TextCellView);
        titleCellView.content(title !== void 0 ? title : "");
      }
    },
    entityDidSetIcon(icon: Graphics | null, entityTrait: EntityTrait): void {
      const rowView = this.owner.row.view;
      if (rowView !== null) {
        const iconCellView = rowView.getOrCreateCell("icon", IconCellView);
        iconCellView.graphics(icon !== null ? icon : this.owner.domainIcon);
      }
    },
  })
  readonly entity!: ControllerTrait<this, EntityTrait>;

  @ControllerTrait<CollectionController, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(statusTrait: StatusTrait | null): void {
      if (statusTrait !== null) {
        this.owner.initStatus(statusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatus(newStatusVector);
    },
  })
  readonly status!: ControllerTrait<this, StatusTrait>;

  @ControllerService<CollectionController, PrismManager>({
    type: PrismManager,
    extends: PrismService,
    observe: false,
  })
  readonly prismService!: PrismService<this>;

  protected override didMount(): void {
    const domainTrait = this.domain.trait;
    if (domainTrait !== null) {
      const model = domainTrait.model;
      if (model !== null) {
        this.injectEntities(model, domainTrait);
      }
    }
    super.didMount();
  }

  @Lazy
  static get domainIcon(): Graphics {
    return VectorIcon.create(24, 24, "M4,4L23,4L20,20L1,20Z");
  }
}
