import {Lazy} from "@swim/util";
import type {
  Model,
  Trait,
  SelectionOptions,
  SelectionManager,
  SelectionManagerObserver,
} from "@swim/model";
import {Feel} from "@swim/theme";
import {View} from "@swim/view";
import {HtmlView} from "@swim/dom";
import {Graphics, VectorIcon, HtmlIconView} from "@swim/graphics";
import {
  ControllerFastener,
  ControllerModel,
  ControllerTrait,
  ControllerView,
  CompositeController,
} from "@swim/controller";
import {DrawerView} from "@swim/window";
import {Status, StatusVector, StatusTrait, EntityTrait, SessionModel} from "@swim/domain";
import {RefractorController} from "../refractor/RefractorController";
import {MagnifierHandle} from "./MagnifierHandle";

export class MagnifierController extends CompositeController {
  constructor() {
    super();
    this.onClickCollapse = this.onClickCollapse.bind(this);
    this.initMagnifier();
  }

  protected initMagnifier(): void {
    this.refractor.injectController();
  }

  protected attachPanel(panelView: DrawerView): void {
    this.handle.injectView(panelView);
    this.fixtures.injectView(panelView);
  }

  protected detachPanel(panelView: DrawerView): void {
    panelView.removeAll();
  }

  protected createHandle(): MagnifierHandle | null {
    const handleView = MagnifierHandle.create();
    handleView.flexShrink.setState(0, View.Intrinsic);

    const collapseButton = HtmlIconView.create();
    collapseButton.graphics.setState(MagnifierController.collapseIcon, View.Intrinsic);
    collapseButton.cursor.setState("pointer", View.Intrinsic);
    collapseButton.on("click", this.onClickCollapse);
    handleView.accessory.setView(collapseButton);
    handleView.accessory.injectView();

    handleView.modifyMood(Feel.default, [[Feel.primary, 1]]);

    return handleView;
  }

  protected initHandle(handleView: MagnifierHandle): void {
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      this.onSetStatus(statusTrait.statusVector);
    } else {
      this.onSetStatus(StatusVector.empty());
    }
  }

  protected createFixtures(): HtmlView | null {
    const fixturesView = HtmlView.create();
    fixturesView.addClass("fixtures");
    fixturesView.position.setState("relative", View.Intrinsic);
    fixturesView.flexGrow.setState(1, View.Intrinsic);
    fixturesView.overflowX.setState("hidden", View.Intrinsic);
    fixturesView.overflowY.setState("auto", View.Intrinsic);
    fixturesView.pointerEvents.setState("auto", View.Intrinsic);
    return fixturesView;
  }

  protected initFixtures(fixturesView: HtmlView): void {
    const refractorController = this.refractor.controller;
    if (refractorController !== null) {
      refractorController.fixtures.setView(fixturesView);
    }
  }

  protected createRefractor(): RefractorController | null {
    return new RefractorController();
  }

  protected initRefractor(refractorController: RefractorController): void {
    const fixturesView = this.fixtures.view;
    if (fixturesView !== null) {
      refractorController.fixtures.setView(fixturesView);
    }
  }

  protected onSelect(model: Model, index: number, options: SelectionOptions | null, selectionManager: SelectionManager): void {
    const entityTrait = model.getTrait(EntityTrait);
    if (entityTrait !== null) {
      this.entity.setTrait(entityTrait);
      const panelView = this.panel.view;
      if (panelView !== null) {
        panelView.present();
      }
    }
  }

  protected onUnselect(model: Model, selectionManager: SelectionManager): void {
    const selections = selectionManager.selections;
    if (selections.length !== 0) {
      this.entity.setTrait(selections[selections.length - 1]!.getTrait(EntityTrait));
    } else {
      const panelView = this.panel.view;
      if (panelView !== null) {
        panelView.dismiss();
      }
      this.entity.setTrait(null);
      this.onSetStatus(StatusVector.empty());
    }
  }

  protected attachEntity(entityTrait: EntityTrait): void {
    this.status.setTrait(entityTrait.getTrait(StatusTrait));
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.label.setView(entityTrait.title);
      handleView.icon.setView(entityTrait.icon);
    }
    const refractorController = this.refractor.controller;
    if (refractorController !== null) {
      refractorController.entity.setTrait(entityTrait);
    }
  }

  protected detachEntity(entityTrait: EntityTrait): void {
    const refractorController = this.refractor.controller;
    if (refractorController !== null) {
      refractorController.entity.setTrait(null);
    }
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.label.setView(null);
      handleView.icon.setView(null);
    }
    this.status.setTrait(null);
  }

  protected onSetEntityTitle(title: string | undefined): void {
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.label.setView(title);
    }
  }

  protected onSetEntityIcon(icon: Graphics | null): void {
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.icon.setView(icon);
    }
  }

  protected initStatus(statusTrait: StatusTrait): void {
    this.onSetStatus(statusTrait.statusVector);
  }

  protected onSetStatus(statusVector: StatusVector): void {
    const handleView = this.handle.view;
    if (handleView !== null) {
      const alert = statusVector.get(Status.alert) || 0;
      const warning = statusVector.get(Status.warning) || 0;
      const inactive = statusVector.get(Status.inactive) || 0;
      if (alert !== 0) {
        handleView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
      } else if (warning !== 0) {
        handleView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
      } else {
        handleView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
      }
      if (inactive !== 0) {
        handleView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
      } else {
        handleView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
      }
    }
  }

  @ControllerView<MagnifierController, DrawerView>({
    type: DrawerView,
    onSetView(newPanelView: DrawerView | null, oldPanelView: DrawerView | null): void {
      if (oldPanelView !== null) {
        this.owner.detachPanel(oldPanelView);
      }
      if (newPanelView !== null) {
        this.owner.attachPanel(newPanelView);
      }
    },
  })
  readonly panel!: ControllerView<this, DrawerView>;

  @ControllerView<MagnifierController, MagnifierHandle>({
    type: MagnifierHandle,
    onSetView(handleView: MagnifierHandle | null): void {
      if (handleView !== null) {
        this.owner.initHandle(handleView);
      }
    },
    createView(): MagnifierHandle | null {
      return this.owner.createHandle();
    },
  })
  readonly handle!: ControllerView<this, MagnifierHandle>;

  @ControllerView<MagnifierController, HtmlView>({
    type: HtmlView,
    onSetView(fixturesView: HtmlView | null): void {
      if (fixturesView !== null) {
        this.owner.initFixtures(fixturesView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createFixtures();
    },
  })
  readonly fixtures!: ControllerView<this, HtmlView>;

  @ControllerFastener<MagnifierController, RefractorController>({
    type: RefractorController,
    onSetController(refractorController: RefractorController | null): void {
      if (refractorController !== null) {
        this.owner.initRefractor(refractorController);
      }
    },
    createController(): RefractorController | null {
      return this.owner.createRefractor();
    }
  })
  readonly refractor!: ControllerFastener<this, RefractorController>;

  @ControllerTrait<MagnifierController, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(newEntityTrait: EntityTrait | null, oldEntityTrait: EntityTrait | null): void {
      if (oldEntityTrait !== null) {
        this.owner.detachEntity(oldEntityTrait);
      }
      if (newEntityTrait !== null) {
        this.owner.attachEntity(newEntityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined, entityTrait: EntityTrait): void {
      this.owner.onSetEntityTitle(title);
    },
    entityDidSetIcon(icon: Graphics | null, entityTrait: EntityTrait): void {
      this.owner.onSetEntityIcon(icon);
    },
    traitDidInsertTrait(memberTrait: Trait, targetTrait: Trait | null): void {
      if (memberTrait instanceof StatusTrait) {
        this.owner.status.setTrait(memberTrait);
      }
    },
  })
  readonly entity!: ControllerTrait<this, EntityTrait>;

  @ControllerTrait<MagnifierController, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(statusTrait: StatusTrait | null): void {
      if (statusTrait !== null) {
        this.owner.initStatus(statusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatus(newStatusVector);
    },
  })
  readonly status!: ControllerTrait<this, StatusTrait>;

  protected initSessionModel(sessionModel: SessionModel): void {
    // hook
  }

  protected attachSessionModel(sessionModel: SessionModel): void {
    const selectionManager = sessionModel.selectionService.manager;
    if (selectionManager !== null) {
      selectionManager.addModelManagerObserver(this.session);
    }
  }

  protected detachSessionModel(sessionModel: SessionModel): void {
    const selectionManager = sessionModel.selectionService.manager;
    if (selectionManager !== null) {
      selectionManager.removeModelManagerObserver(this.session);
    }
 }

  protected willSetSessionModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
    // hook
  }

  protected onSetSessionModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
    if (oldSessionModel !== null) {
      this.detachSessionModel(oldSessionModel);
    }
    if (newSessionModel !== null) {
      this.attachSessionModel(newSessionModel);
      this.initSessionModel(newSessionModel);
    }
  }

  protected didSetSessionModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
    // hook
  }

  @ControllerModel<MagnifierController, SessionModel, never, SelectionManagerObserver>({
    type: SessionModel,
    willSetModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
      this.owner.willSetSessionModel(newSessionModel, oldSessionModel);
    },
    onSetModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
      this.owner.onSetSessionModel(newSessionModel, oldSessionModel);
    },
    didSetModel(newSessionModel: SessionModel | null, oldSessionModel: SessionModel | null): void {
      this.owner.didSetSessionModel(newSessionModel, oldSessionModel);
    },
    selectionManagerDidSelect(model: Model, index: number, options: SelectionOptions | null, selectionManager: SelectionManager): void {
      this.owner.onSelect(model, index, options, selectionManager);
    },
    selectionManagerDidUnselect(model: Model, selectionManager: SelectionManager): void {
      this.owner.onUnselect(model, selectionManager);
    },
  })
  readonly session!: ControllerModel<this, SessionModel> & SelectionManagerObserver;

  protected onClickCollapse(event: MouseEvent): void {
    event.stopPropagation();
    //const panelView = this.panel.view;
    //if (panelView !== null) {
    //  panelView.hide();
    //}
    const sessionModel = this.session.model;
    if (sessionModel !== null) {
      sessionModel.selectionService.unselectAll();
    }
  }

  @Lazy
  static get collapseIcon(): Graphics {
    return VectorIcon.create(24, 24, "M16.6,8.6L12,13.2L7.4,8.6L6,10L12,16L18,10Z");
  }
}
