import {Model, Trait, SelectionOptions, SelectableTrait} from "@swim/model";
import {Look, Feel} from "@swim/theme";
import {View, PositionGestureInput} from "@swim/view";
import {Graphics, Icon, FilledIcon, PolygonIcon, EnclosedIcon} from "@swim/graphics";
import {
  ControllerModel,
  ControllerTrait,
  ControllerView,
  ControllerFastener,
  CompositeController,
} from "@swim/controller";
import {TextCellView, IconCellView, DisclosureCellView, LeafView, RowView} from "@swim/table";
import {
  Status,
  StatusVector,
  StatusTrait,
  IndicatorTrait,
  IndicatorGroup,
  IndicatedTrait,
  EntityTrait,
  EntityGroup,
} from "@swim/domain";
import {CatalogIndicatorCell} from "./CatalogIndicatorCell";
import {CatalogEntityTree} from "./CatalogEntityTree";

export class CatalogEntityLimb extends CompositeController {
  protected initLimb(rowView: RowView): void {
    this.leaf.injectView(rowView);
    rowView.head.injectView();
    rowView.foot.injectView();

    const entityGroup = this.subentities.model;
    if (entityGroup !== null) {
      const entityTree = this.subtree.injectController();
      if (entityTree !== null) {
        if (rowView.disclosure.isExpanded()) {
          entityTree.tree.injectView(rowView);
        }
        entityTree.entities.setModel(entityGroup);
      }
    }
  }

  protected initLeaf(treeLeaf: LeafView): void {
    this.iconCell.injectView(treeLeaf);
    this.titleCell.injectView(treeLeaf);
    const childControllers = this.childControllers;
    for (let i = 0; i < childControllers.length; i += 1) {
      const childController = childControllers[i];
      if (childController instanceof CatalogIndicatorCell) {
        this.onInsertIndicatorCell(childController);
      }
    }
    this.disclosureCell.injectView(treeLeaf);
    const entityGroup = this.subentities.model;
    if (entityGroup === null) {
      const disclosureCell = this.disclosureCell.view;
      if (disclosureCell !== null) {
        disclosureCell.display.setState("none");
      }
    }

    const selectableTrait = this.selectable.trait;
    if (selectableTrait !== null) {
      if (selectableTrait.isSelected()) {
        treeLeaf.highlight.focus();
      } else {
        treeLeaf.highlight.unfocus();
      }
    }

    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      this.onSetStatus(statusTrait.statusVector);
    }
  }

  protected createIconCell(): IconCellView {
    const icon = EnclosedIcon.embossed(PolygonIcon.create(6), null)
                             .withInnerScale(Math.sqrt(2) / 2);
    const iconCell = IconCellView.create()
      .iconWidth(40)
      .iconHeight(40)
      .graphics(icon);
    return iconCell;
  }

  protected initIconCell(iconCell: IconCellView): void {
    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      let icon = entityTrait.icon;
      if (icon instanceof Icon) {
        if (icon instanceof FilledIcon) {
          icon = icon.withFillLook(Look.accentColor);
        }
      } else {
        icon = null;
      }
      const cellIcon = iconCell.graphics.state;
      if (cellIcon instanceof EnclosedIcon) {
        iconCell.graphics.setState(cellIcon.withInner(icon as Icon | null));
      }
    }
  }

  protected initTitleCell(titleCell: TextCellView): void {
    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      let entityTitle = entityTrait.title;
      if (entityTitle === void 0) {
        entityTitle = "";
      }
      titleCell.content(entityTitle);
    }
  }

  protected initDisclosureCell(disclosureCell: DisclosureCellView): void {
    // hook
  }

  protected initEntity(entityTrait: EntityTrait): void {
    const titleCell = this.titleCell.view;
    if (titleCell !== null) {
      let entityTitle = entityTrait.title;
      if (entityTitle === void 0) {
        entityTitle = "";
      }
      titleCell.content(entityTitle);
    }

    this.selectable.setTrait(entityTrait.getTrait(SelectableTrait));
    this.status.setTrait(entityTrait.getTrait(StatusTrait));
    this.subentities.setModel(entityTrait.subentities.model);
    const indicatedTrait = entityTrait.getTrait(IndicatedTrait);
    if (indicatedTrait !== null) {
      this.indicators.setModel(indicatedTrait.indicators.model);
    }
  }

  protected initSelectable(selectableTrait: SelectableTrait): void {
    const treeLeaf = this.leaf.view;
    if (treeLeaf !== null) {
      if (selectableTrait.isSelected()) {
        treeLeaf.highlight.focus();
      } else {
        treeLeaf.highlight.unfocus();
      }
    }
  }

  protected initStatus(statusTrait: StatusTrait): void {
    this.onSetStatus(statusTrait.statusVector);
  }

  protected onInsertSubentities(entityGroup: EntityGroup): void {
    this.subentities.setModel(entityGroup);
  }

  protected onRemoveSubentities(entityGroup: EntityGroup): void {
    this.subentities.removeModel();
  }

  protected attachSubentities(entityGroup: EntityGroup): void {
    const disclosureCell = this.disclosureCell.view;
    if (disclosureCell !== null) {
      disclosureCell.display.setPrecedence(View.Intrinsic);
      const treeLeaf = this.leaf.view;
      if (treeLeaf !== null) {
        treeLeaf.requireUpdate(View.NeedsResize | View.NeedsLayout);
      }
    }

    const rowView = this.row.view;
    if (rowView !== null) {
      const entityTree = this.subtree.injectController();
      if (entityTree !== null) {
        if (rowView.disclosure.isExpanded()) {
          entityTree.tree.injectView(rowView);
        }
        entityTree.entities.setModel(entityGroup);
      }
    }
  }

  protected detachSubentities(entityGroup: EntityGroup): void {
    const disclosureCell = this.disclosureCell.view;
    if (disclosureCell !== null) {
      disclosureCell.display.setState("none");
    }
  }

  protected onSetTitle(title: string | undefined): void {
    const titleCell = this.titleCell.view;
    if (titleCell !== null) {
      titleCell.content(title !== void 0 ? title : "");
    }
  }

  protected onSetIcon(icon: Graphics | null): void {
    const iconCell = this.iconCell.view;
    if (iconCell !== null) {
      if (icon instanceof Icon) {
        if (icon instanceof FilledIcon) {
          icon = icon.withFillLook(Look.accentColor);
        }
      } else {
        icon = null;
      }
      const cellIcon = iconCell.graphics.state;
      if (cellIcon instanceof EnclosedIcon) {
        iconCell.graphics.setState(cellIcon.withInner(icon as Icon | null));
      }
    }
  }

  protected onSetStatus(statusVector: StatusVector): void {
    const treeLeaf = this.leaf.view;
    if (treeLeaf !== null) {
      const alert = statusVector.get(Status.alert) || 0;
      const warning = statusVector.get(Status.warning) || 0;
      const inactive = statusVector.get(Status.inactive) || 0;
      if (alert !== 0) {
        treeLeaf.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
      } else if (warning !== 0) {
        treeLeaf.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
      } else {
        treeLeaf.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
      }
      if (inactive !== 0) {
        treeLeaf.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
      } else {
        treeLeaf.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
      }
    }
  }

  protected onSelect(options: SelectionOptions | null, selectableTrait: SelectableTrait): void {
    const treeLeaf = this.leaf.view;
    if (treeLeaf !== null) {
      treeLeaf.highlight.focus();
    }
  }

  protected onUnselect(selectableTrait: SelectableTrait): void {
    const treeLeaf = this.leaf.view;
    if (treeLeaf !== null) {
      treeLeaf.highlight.unfocus();
    }
  }

  protected initIndicators(indicatorGroup: IndicatorGroup): void {
    const childModels = indicatorGroup.childModels;
    for (let i = 0, n = childModels.length; i < n; i += 1) {
      const childModel = childModels[i]!;
      const indicatorTrait = childModel.getTrait(IndicatorTrait);
      if (indicatorTrait !== null) {
        this.onInsertIndicator(indicatorTrait, null);
      }
    }

    const treeLeaf = this.leaf.view;
    if (treeLeaf !== null && treeLeaf.isMounted() && !treeLeaf.isCulled()) {
      this.startConsumingIndicators();
    }
  }

  protected onInsertIndicator(childIndicator: IndicatorTrait, targetIndicator: IndicatorTrait | null): void {
    const key = childIndicator.indicatorType.key;
    let controllerFastener = this.getControllerFastener(key) as ControllerFastener<this, CatalogIndicatorCell> | null;
    if (controllerFastener === null) {
      controllerFastener = new CatalogEntityLimb.IndicatorFastener(this, key, "indicator") as ControllerFastener<this, CatalogIndicatorCell>;
      this.setControllerFastener(key, controllerFastener);
      const indicatorCell = new CatalogIndicatorCell();
      indicatorCell.indicator.setTrait(childIndicator);
      const targetController = targetIndicator !== null ? this.getChildController(targetIndicator.indicatorType.key) : null;
      this.insertChildController(indicatorCell, targetController, key);
    }
  }

  protected onRemoveIndicator(childIndicator: IndicatorTrait): void {
    const key = childIndicator.indicatorType.key;
    const controllerFastener = this.getControllerFastener(key) as ControllerFastener<this, CatalogIndicatorCell> | null;
    if (controllerFastener !== null) {
      controllerFastener.removeController();
      this.setControllerFastener(key, null);
    }
  }

  protected onInsertIndicatorCell(indicatorCell: CatalogIndicatorCell): void {
    const treeLeaf = this.leaf.view;
    if (treeLeaf !== null) {
      const treeCell = indicatorCell.cell.createView();
      if (treeCell !== null) {
        const targetLens = this.nextChildController(indicatorCell);
        const targetView = targetLens instanceof CatalogIndicatorCell ? targetLens.cell.view : null;
        const id = indicatorCell.indicator.trait!.indicatorType.key;
        treeLeaf.insertChildView(treeCell, targetView, id);
        indicatorCell.cell.setView(treeCell);
      }
    }
  }

  protected onRemoveIndicatorCell(indicatorCell: CatalogIndicatorCell): void {
    const treeCell = indicatorCell.cell.view;
    if (treeCell !== null) {
      treeCell.remove();
    }
  }

  protected onMountLimb(rowView: RowView): void {
    if (rowView.disclosure.isExpanded()) {
      this.startConsumingSubentities();
      const entityTree = this.subtree.controller;
      if (entityTree !== null) {
        entityTree.tree.injectView(rowView);
      }
    }
  }

  protected onUnmountLimb(rowView: RowView): void {
    this.stopConsumingSubentities();
    const entityTree = this.subtree.controller;
    if (entityTree !== null) {
      entityTree.tree.removeView();
    }
  }

  protected onExpandLimb(rowView: RowView): void {
    if (rowView.isMounted()) {
      this.startConsumingSubentities();
      const entityTree = this.subtree.controller;
      if (entityTree !== null) {
        entityTree.tree.injectView(rowView);
      }
    }
  }

  protected onCollapseLimb(rowView: RowView): void {
    this.stopConsumingSubentities();
    const entityTree = this.subtree.controller;
    if (entityTree !== null) {
      entityTree.tree.removeView();
    }
  }

  protected onPressLeaf(input: PositionGestureInput, event: Event | null, treeLeaf: LeafView): void {
    const selectableTrait = this.selectable.trait;
    if (selectableTrait !== null) {
      if (!selectableTrait.isSelected()) {
        selectableTrait.select({multi: input.shiftKey});
      } else if (input.shiftKey) {
        selectableTrait.unselect();
      } else {
        selectableTrait.unselectAll();
      }
    }
  }

  protected onLongPressLeaf(input: PositionGestureInput, treeLeaf: LeafView): void {
    input.preventDefault();
    const selectableTrait = this.selectable.trait;
    if (selectableTrait !== null) {
      if (!selectableTrait.isSelected()) {
        selectableTrait.select({multi: true});
      } else {
        selectableTrait.unselect();
      }
    }
  }

  protected initSubtree(entityTree: CatalogEntityTree): void {
    // hook
  }

  protected startConsumingSubentities(): void {
    const entityGroup = this.subentities.model;
    if (entityGroup !== null) {
      entityGroup.addModelConsumer(this);
    }
  }

  protected stopConsumingSubentities(): void {
    const entityGroup = this.subentities.model;
    if (entityGroup !== null) {
      entityGroup.removeModelConsumer(this);
    }
  }

  protected startConsumingIndicators(): void {
    const indicatorGroup = this.indicators.model;
    if (indicatorGroup !== null) {
      indicatorGroup.addModelConsumer(this);
    }
  }

  protected stopConsumingIndicators(): void {
    const indicatorGroup = this.indicators.model;
    if (indicatorGroup !== null) {
      indicatorGroup.removeModelConsumer(this);
    }
  }

  @ControllerView<CatalogEntityLimb, RowView>({
    type: RowView,
    observe: true,
    onSetView(rowView: RowView | null): void {
      if (rowView !== null) {
        this.owner.initLimb(rowView);
        if (rowView.isMounted()) {
          this.owner.onMountLimb(rowView);
        }
      }
    },
    viewDidMount(rowView: RowView): void {
      this.owner.onMountLimb(rowView);
    },
    viewWillUnmount(rowView: RowView): void {
      this.owner.onUnmountLimb(rowView);
    },
    viewWillExpand(rowView: RowView): void {
      this.owner.onExpandLimb(rowView);
    },
    viewDidCollapse(rowView: RowView): void {
      this.owner.onCollapseLimb(rowView);
    },
  })
  readonly row!: ControllerView<this, RowView>;

  @ControllerView<CatalogEntityLimb, LeafView>({
    key: true,
    type: LeafView,
    observe: true,
    onSetView(treeLeaf: LeafView | null): void {
      if (treeLeaf !== null) {
        this.owner.initLeaf(treeLeaf);
        if (treeLeaf.isMounted() && !treeLeaf.isCulled()) {
          this.owner.startConsumingIndicators();
        }
      }
    },
    viewDidMount(treeLeaf: LeafView): void {
      const rowView = this.owner.row.view;
      if (rowView !== null && rowView.visibility.value !== "hidden" && !rowView.isCulled()) {
        this.owner.startConsumingIndicators();
      }
    },
    viewWillUnmount(treeLeaf: LeafView): void {
      this.owner.stopConsumingIndicators();
    },
    viewDidCull(treeLeaf: LeafView): void {
      this.owner.stopConsumingIndicators();
    },
    viewWillUncull(treeLeaf: LeafView): void {
      this.owner.startConsumingIndicators();
    },
    viewDidPress(input: PositionGestureInput, event: Event | null, treeLeaf: LeafView): void {
      this.owner.onPressLeaf(input, event, treeLeaf);
    },
    viewDidLongPress(input: PositionGestureInput, treeLeaf: LeafView): void {
      this.owner.onLongPressLeaf(input, treeLeaf);
    },
  })
  readonly leaf!: ControllerView<this, LeafView>;

  @ControllerView<CatalogEntityLimb, IconCellView>({
    key: "icon",
    type: IconCellView,
    onSetView(iconCell: IconCellView | null): void {
      if (iconCell !== null) {
        this.owner.initIconCell(iconCell);
      }
    },
    createView(): IconCellView | null {
      return this.owner.createIconCell();
    },
  })
  readonly iconCell!: ControllerView<this, IconCellView>;

  @ControllerView<CatalogEntityLimb, TextCellView>({
    key: "title",
    type: TextCellView,
    onSetView(titleCell: TextCellView | null): void {
      if (titleCell !== null) {
        this.owner.initTitleCell(titleCell);
      }
    },
  })
  readonly titleCell!: ControllerView<this, TextCellView>;

  static IndicatorFastener = ControllerFastener.define<CatalogEntityLimb, CatalogIndicatorCell>({
    type: CatalogIndicatorCell,
    onSetController(newIndicatorCell: CatalogIndicatorCell | null, oldIndicatorCell: CatalogIndicatorCell | null): void {
      if (oldIndicatorCell !== null) {
        this.owner.onRemoveIndicatorCell(oldIndicatorCell);
      }
      if (newIndicatorCell !== null) {
        this.owner.onInsertIndicatorCell(newIndicatorCell);
      }
    },
  });

  @ControllerView<CatalogEntityLimb, DisclosureCellView>({
    key: "disclose",
    type: DisclosureCellView,
    onSetView(disclosureCell: DisclosureCellView | null): void {
      if (disclosureCell !== null) {
        this.owner.initDisclosureCell(disclosureCell);
      }
    },
  })
  readonly disclosureCell!: ControllerView<this, DisclosureCellView>;

  @ControllerFastener<CatalogEntityLimb, CatalogEntityTree>({
    type: CatalogEntityTree,
    onSetController(entityTree: CatalogEntityTree | null): void {
      if (entityTree !== null) {
        this.owner.initSubtree(entityTree);
      }
    },
    createController(): CatalogEntityTree | null {
      return new CatalogEntityTree();
    },
  })
  readonly subtree!: ControllerFastener<this, CatalogEntityTree>;

  @ControllerTrait<CatalogEntityLimb, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined): void {
      this.owner.onSetTitle(title);
    },
    entityDidSetIcon(icon: Graphics | null): void {
      this.owner.onSetIcon(icon);
    },
    traitDidInsertChildModel(childModel: Model, targetModel: Model | null): void {
      if (childModel.key === "subentities" && childModel instanceof EntityGroup) {
        this.owner.onInsertSubentities(childModel);
      }
    },
    traitDidRemoveChildModel(childModel: Model): void {
      if (childModel.key === "subentities" && childModel instanceof EntityGroup) {
        this.owner.onRemoveSubentities(childModel);
      }
    },
    traitDidInsertTrait(memberTrait: Trait, targetTrait: Trait | null): void {
      if (memberTrait instanceof SelectableTrait) {
        this.owner.selectable.setTrait(memberTrait);
      }
    },
  })
  readonly entity!: ControllerTrait<this, EntityTrait>;

  @ControllerTrait<CatalogEntityLimb, SelectableTrait>({
    type: SelectableTrait,
    observe: true,
    onSetTrait(selectableTrait: SelectableTrait | null): void {
      if (selectableTrait !== null) {
        this.owner.initSelectable(selectableTrait);
      }
    },
    traitDidSelect(options: SelectionOptions | null, selectableTrait: SelectableTrait): void {
      this.owner.onSelect(options, selectableTrait);
    },
    traitWillUnselect(selectableTrait: SelectableTrait): void {
      this.owner.onUnselect(selectableTrait);
    },
  })
  readonly selectable!: ControllerTrait<this, SelectableTrait>;

  @ControllerTrait<CatalogEntityLimb, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(statusTrait: StatusTrait | null): void {
      if (statusTrait !== null) {
        this.owner.initStatus(statusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatus(newStatusVector);
    },
  })
  readonly status!: ControllerTrait<this, StatusTrait>;

  @ControllerModel<CatalogEntityLimb, EntityGroup>({
    type: EntityGroup,
    onSetModel(newEntityGroup: EntityGroup | null, oldEntityGroup: EntityGroup | null): void {
      if (oldEntityGroup !== null) {
        this.owner.detachSubentities(oldEntityGroup);
      }
      if (newEntityGroup !== null) {
        this.owner.attachSubentities(newEntityGroup);
      }
    },
  })
  readonly subentities!: ControllerModel<this, EntityGroup>;

  @ControllerModel<CatalogEntityLimb, IndicatorGroup>({
    type: IndicatorGroup,
    observe: true,
    onSetModel(indicatorGroup: IndicatorGroup | null): void {
      if (indicatorGroup !== null) {
        this.owner.initIndicators(indicatorGroup);
      }
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null): void {
      const childIndicator = childModel.getTrait(IndicatorTrait);
      if (childIndicator !== null) {
        const targetIndicator = targetModel !== null ? targetModel.getTrait(IndicatorTrait) : null;
        this.owner.onInsertIndicator(childIndicator, targetIndicator);
      }
    },
    modelWillRemoveChildModel(childModel: Model): void {
      const childIndicator = childModel.getTrait(IndicatorTrait);
      if (childIndicator !== null) {
        this.owner.onRemoveIndicator(childIndicator);
      }
    },
  })
  readonly indicators!: ControllerModel<this, IndicatorGroup>;
}
