import {View} from "@swim/view";
import {VectorIcon} from "@swim/graphics";
import {ControllerTrait, ControllerView, CompositeController} from "@swim/controller";
import {TextCellView, IconCellView, RowView} from "@swim/table";
import {EntityTrait} from "@swim/domain";

export class SuggestionController extends CompositeController {
  protected initRowView(rowView: RowView): void {
    rowView.rowHeight(32);

    const iconCellView = rowView.getOrCreateCell("icon", IconCellView)
        .iconWidth(24, View.Intrinsic)
        .iconHeight(24, View.Intrinsic);
    const entityIcon = VectorIcon.create(24, 24, "M8,8 L19,8 L16,16 L5,16 Z");
    iconCellView.graphics(entityIcon, View.Intrinsic);

    const titleCellView = rowView.getOrCreateCell("title", TextCellView);
    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      let entityTitle = entityTrait.title;
      if (entityTitle === void 0) {
        entityTitle = "";
      }
      titleCellView.content(entityTitle);
    }
  }

  protected initEntity(entityTrait: EntityTrait): void {
    const rowView = this.row.view;
    if (rowView !== null) {
      const titleCellView = rowView.getOrCreateCell("title", TextCellView);
      let entityTitle = entityTrait.title;
      if (entityTitle === void 0) {
        entityTitle = "";
      }
      titleCellView.content(entityTitle);
    }
  }

  @ControllerView<SuggestionController, RowView>({
    type: RowView,
    onSetView(rowView: RowView | null): void {
      if (rowView !== null) {
        this.owner.initRowView(rowView);
      }
    },
  })
  readonly row!: ControllerView<this, RowView>;

  @ControllerTrait<SuggestionController, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined, entityTrait: EntityTrait): void {
      const rowView = this.owner.row.view;
      if (rowView !== null) {
        const titleCellView = rowView.getOrCreateCell("title", TextCellView);
        titleCellView.content(title !== void 0 ? title : "");
      }
    },
  })
  readonly entity!: ControllerTrait<this, EntityTrait>;
}
